import { createSelector } from 'reselect';

const getUserPhone = state => state.Enrollment.userToEnroll.phone;

export const getPhoneIndex = createSelector(
  getUserPhone,
  phone => (phone.length > 0 ? phone.substring(0, 4) : ''),
);

export const getPhoneWithoutIndex = createSelector(
  getUserPhone,
  phone => (phone.length > 0 ? phone.substring(4, phone.length) : ''),
);
