import { put, takeLatest, call, all } from 'redux-saga/effects';
import Enrollment from '../../services/apis/enrollment';
import { actionCreator } from '../../utils/generators';
import types from './enrollment.types';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* step1(step1Action) {
  try {
    const step1Response = yield call(
      new Enrollment().step1,
      step1Action.payload,
    );
    yield put({
      type: actionCreator('res', types.STEP1),
      payload: step1Response.data,
    });
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP1),
      payload: error.payload.text,
    });
  }
}
function* step2(step2Action) {
  try {
    const step2Response = yield call(
      new Enrollment().step2,
      step2Action.payload,
    );
    yield all([
      put({ type: actionCreator('req', types.NEXT_STEP) }),
      put({
        type: actionCreator('res', types.STEP2),
        payload: step2Response.data.hash,
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP2),
      payload: error.payload.text,
    });
  }
}
function* step3(step3Action) {
  try {
    const step3Response = yield call(
      new Enrollment().step3,
      step3Action.payload,
    );
    yield all([
      put({ type: actionCreator('req', types.NEXT_STEP) }),
      put({
        type: actionCreator('res', types.STEP3),
        payload: step3Response.data,
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP3),
      payload: error.payload.text,
    });
  }
}
function* retryStep3(retryStep3Action) {
  try {
    const retryStep3Response = yield call(
      new Enrollment().retryStep3,
      retryStep3Action.payload,
    );
    yield put({
      type: actionCreator('res', types.RETRY_STEP3),
      payload: retryStep3Response.data,
    });
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.RETRY_STEP3),
      payload: error.payload.text,
    });
  }
}
function* step4(step4Action) {
  try {
    yield call(new Enrollment().step4, step4Action.payload);
    yield all([
      put({
        type: actionCreator('res', types.STEP4),
      }),
      put({
        type: actionCreator('req', types.TOGGLE_MODAL),
        payload: { enrollmentSuccess: true },
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP4),
      payload: error.payload.text,
    });
  }
}
export function* enrollmentSagas() {
  yield takeLatest(actionCreator('req', types.STEP1), sagaWithLoader, step1);
  yield takeLatest(actionCreator('req', types.STEP2), sagaWithLoader, step2);
  yield takeLatest(actionCreator('req', types.STEP3), sagaWithLoader, step3);
  yield takeLatest(
    actionCreator('req', types.RETRY_STEP3),
    sagaWithLoader,
    retryStep3,
  );
  yield takeLatest(actionCreator('req', types.STEP4), sagaWithLoader, step4);
}
