import React from 'react';
import EnrollmentStep1 from './EnrollmentStep1';
import EnrollmentStep2 from './EnrollmentStep2';
import EnrollmentStep3 from './EnrollmentStep3';
import EnrollmentStep4 from './EnrollmentStep4';

const CurrentEnrollmentStep = props => {
  switch (props.activeStep) {
    case 1:
      return <EnrollmentStep1 formRefSetter={props.formRefSetter} />;
    case 2:
      return <EnrollmentStep2 formRefSetter={props.formRefSetter} />;
    case 3:
      return <EnrollmentStep3 formRefSetter={props.formRefSetter} />;
    case 4:
      return <EnrollmentStep4 formRefSetter={props.formRefSetter} />;
    default:
      return null;
  }
};

export default CurrentEnrollmentStep;
