import { ErrorHandler } from './ErrorHandler';

export default class Checks {
  static ordersList = [
    {
      account: '73270330000',
      checkBookType: 'Standard 25',
      number: '4',
      sendingDate: '20191015',
      branch: 'Agence Centrale',
    },
    {
      account: '13270339000',
      checkBookType: 'Standard 25',
      number: '5',
      sendingDate: '20190215',
      branch: 'Agence Centrale',
    },
  ];

  _handleOnSuccess(response, resolver) {
    return resolver({ data: response.data, status: response.status });
  }
  _handleOnError(error, reject) {
    return reject(ErrorHandler(error));
  }

  mockGetOrders = () =>
    new Promise((resolver, reject) =>
      setTimeout(() => {
        const response = {
          data: Checks.ordersList,
          status: 200,
        };
        this._handleOnSuccess(response, resolver);
      }, 1500),
    );

  mockSaveOrder = data =>
    new Promise((resolver, reject) =>
      setTimeout(() => {
        Checks.ordersList.push(data);
        const response = {
          data: data,
          status: 200,
        };
        this._handleOnSuccess(response, resolver);
      }, 1000),
    );
}
