import { actionCreator } from '../../utils/generators';
import types from './enrollment.types';
import produce from 'immer';

const defaultState = {
  isLoading: false,
  error: null,
  modalShown: false,
  activeStep: 1,
  enrollmentSuccess: false,
  userToEnroll: {
    phone: '',
    bank: '',
    birthdate: '',
    email: '',
    accountNumber: '',
    step2Hash: '',
    step3Hash: '',
    activityId: '',
    otp: '',
    deviceId: '1111',
  },
};

const enrollmentReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.STEP1):
    case actionCreator('req', types.STEP2):
    case actionCreator('req', types.STEP3):
    case actionCreator('req', types.RETRY_STEP3):
    case actionCreator('req', types.STEP4):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('req', types.TOGGLE_MODAL):
      draft.isLoading = true;
      draft.error = null;
      draft.modalShown = !draft.modalShown;
      draft.activeStep = 1;
      draft.userToEnroll = defaultState.userToEnroll;
      draft.enrollmentSuccess = action.payload
        ? action.payload.enrollmentSuccess
        : false;
      break;
    case actionCreator('req', types.NEXT_STEP):
      draft.isLoading = true;
      draft.error = null;
      draft.activeStep++;
      break;
    case actionCreator('req', types.PREVIOUS_STEP):
      draft.isLoading = true;
      draft.error = null;
      if (draft.activeStep === 3) draft.activeStep--;
      draft.activeStep--;
      break;
    case actionCreator('req', types.FILL_USER_DATA):
      draft.isLoading = true;
      draft.error = null;
      draft.userToEnroll = { ...draft.userToEnroll, ...action.payload };
      break;
    case actionCreator('res', types.STEP1):
      const STEPS = {
        SIGNALITIC: 'SIGNALITIC',
        DEFINE_MDP: 'DEFINE_MDP',
        GO_TO_AGENCY: 'GO_TO_AGENCY',
      };
      const nextStep = action.payload.nextStep;
      draft.isLoading = false;
      draft.error = null;
      switch (nextStep) {
        case STEPS.SIGNALITIC:
          draft.activeStep = 2;
          break;
        case STEPS.DEFINE_MDP:
          draft.activeStep = 3;
          draft.userToEnroll.step2Hash = action.payload.hash;
          break;
        case STEPS.GO_TO_AGENCY:
          draft.activeStep = 2;
          break;
        default:
          draft.activeStep++;
      }
      break;
    case actionCreator('res', types.STEP2):
      draft.isLoading = false;
      draft.error = null;
      draft.userToEnroll.step2Hash = action.payload;
      break;
    case actionCreator('res', types.STEP3):
      draft.isLoading = false;
      draft.error = null;
      draft.userToEnroll.step3Hash = action.payload.hash;
      draft.userToEnroll.activityId = action.payload.activityId;
      break;
    case actionCreator('res', types.RETRY_STEP3):
      draft.isLoading = false;
      draft.error = null;
      draft.userToEnroll.activityId = action.payload.activityId;
      break;
    case actionCreator('res', types.STEP4):
      draft.isLoading = false;
      draft.error = null;
      break;
    case actionCreator('fail', types.STEP1):
    case actionCreator('fail', types.STEP2):
    case actionCreator('fail', types.STEP3):
    case actionCreator('fail', types.RETRY_STEP3):
    case actionCreator('fail', types.STEP4):
      draft.isLoading = false;
      draft.error = action.payload;
      break;
  }
}, defaultState);
export default enrollmentReducer;
