import { connect } from 'react-redux';
import Enrollment from './components/feature';
import {
  toggleEnrollmentModal,
  previousStep,
} from '../../redux/enrollment/enrollment.actions';

const mapStateToProps = state => ({
  modalShown: state.Enrollment.modalShown,
  activeStep: state.Enrollment.activeStep,
  error: state.Enrollment.error,
});
const mapDispatchToProps = dispatch => ({
  toggleModal: () => {
    dispatch(toggleEnrollmentModal());
  },
  previousStep: () => {
    dispatch(previousStep());
  },
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Enrollment);
