import types from './menu.types';
import produce from 'immer';
import { actionCreator } from '../../utils/generators';

const defaultState = {
  accountsActiveTab: 'operations',
  transfersActiveTab: 'favorites',
  simulatorActiveTab: 'consumerLoan',
  cardsActiveTab: 'cardsDetails',
  checksActiveTab: 'checkBooks',
  activeTabParams: { fromFavorites: false },
  isLoading: false,
  showUpdatePasswordModal: false,
  passwordIsChanged: false,
  error: null,
};

const menuReducer = produce((draft, action) => {
  switch (action.type) {
    case types.ACTIVATE_ACCOUNTS_TAB:
      draft.accountsActiveTab = action.payload;
      break;
    case types.ACTIVATE_TRANSFERS_TAB:
      draft.transfersActiveTab = action.payload;
      draft.activeTabParams.fromFavorites = false;
      break;
    case types.ACTIVATE_CARDS_TAB:
      draft.cardsActiveTab = action.payload;
      break;
    case types.ACTIVATE_CHECKS_TAB:
      draft.checksActiveTab = action.payload;
      break;
    case types.ACTIVATE_TAB_WITH_PARAMS:
      draft.transfersActiveTab = action.payload.activeTab;
      draft.activeTabParams = action.payload.activetabParams;
      break;
    case types.ACTIVATE_SIMULATOR_TAB:
      draft.simulatorActiveTab = action.payload;
      break;
    case actionCreator('req', types.TOGGLE_UPDATE_PASSWORD_MODAL):
      draft.isLoading = true;
      draft.error = null;
      draft.showUpdatePasswordModal = !draft.showUpdatePasswordModal;
      draft.passwordIsChanged = action.payload ? action.payload.success : false;
      break;
    case actionCreator('req', types.UPDATE_PASSWORD_ACTION):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.UPDATE_PASSWORD_ACTION):
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', types.UPDATE_PASSWORD_ACTION):
      draft.error = action.payload;
      draft.isLoading = false;
      break;
    default:
      break;
  }
}, defaultState);
export default menuReducer;
