import React, { Suspense, lazy } from 'react';
import { Route, Switch, Router } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import history from '../utils/history';
import ConnectedLayout from '../components/ConnectedLayout/ConnectedLayout';
import DisconnectedLayout from '../components/DisconnectedLayout/DisconnectedLayout';
import MessageHandler from '../components/MessageHandler/MessageHandler';
import Loader from '../components/Loader/Loader';
import RoutingLoader from '../components/RoutingLoader/RoutingLoader';
import i18next from 'i18next';

const Dashboard = lazy(() => import('../features/Dashboard'));
const Home = lazy(() => import('../features/Home'));
const Login = lazy(() => import('../features/Login'));
const Transfers = lazy(() => import('../features/Transfers'));
const Accounts = lazy(() => import('../features/Accounts'));
const Simulator = lazy(() => import('../features/Simulator'));
const Cards = lazy(() => import('../features/Cards'));
const Checks = lazy(() => import('../features/Checks'));
const FAQ = lazy(() => import('../features/FAQ'));
const Map = lazy(() => import('../features/GeoLocalisation'));
const FrCGU = lazy(() => import('../components/CGU/FrCGU.jsx'));
const EnCGU = lazy(() => import('../components/CGU/EnCGU.jsx'));

export default withTranslation()(() => (
  <main className="app">
    <div className="page">
      <Suspense fallback={<RoutingLoader />}>
        <MessageHandler />
        <Loader />
        <Router history={history}>
          <Switch>
            <Route exact path="/login" render={props => <Login {...props} />} />
            <Route
              exact
              path="/map"
              render={props => (
                <DisconnectedLayout>
                  <Map {...props} />
                </DisconnectedLayout>
              )}
            />
            <Route
              exact
              path="/CGU"
              render={props => {
                const currentLanguage = i18next.language;
                switch (currentLanguage) {
                  case 'fr':
                    return (
                      <DisconnectedLayout>
                        <FrCGU {...props} />
                      </DisconnectedLayout>
                    );
                  case 'en':
                    return (
                      <DisconnectedLayout>
                        <EnCGU {...props} />
                      </DisconnectedLayout>
                    );
                  default:
                    return (
                      <DisconnectedLayout>
                        <FrCGU {...props} />
                      </DisconnectedLayout>
                    );
                }
              }}
            />
            <Route
              exact
              path="/"
              render={props => (
                <DisconnectedLayout hasFooter>
                  <Home {...props} />
                </DisconnectedLayout>
              )}
            />
            <Route
              exact
              path="/faq"
              render={props => (
                <DisconnectedLayout>
                  <FAQ {...props} />
                </DisconnectedLayout>
              )}
            />
            <ConnectedLayout>
              <Route
                exact
                path="/dashboard"
                render={props => <Dashboard {...props} />}
              />
              <Route
                exact
                path="/transfers"
                render={props => <Transfers {...props} />}
              />
              <Route
                exact
                path="/accounts"
                render={props => <Accounts {...props} />}
              />
              <Route
                exact
                path="/cards"
                render={props => <Cards {...props} />}
              />
              <Route
                exact
                path="/checks"
                render={props => <Checks {...props} />}
              />
              <Route
                exact
                path="/simulator"
                render={props => <Simulator {...props} />}
              />
            </ConnectedLayout>
          </Switch>
        </Router>
      </Suspense>
    </div>
  </main>
));
