import produce from 'immer';
import { actionCreator } from '../../utils/generators';
import types from './checks.types';

const defaultState = {
  isLoading: false,
  error: null,
  activeStep: 1,
  branches: [],
  checkToOrder: {
    accountNumber: '',
    nature: '',
    quantity: '',
    branch: '',
  },
  orders: [],
};

const checksReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.NEXT_STEP):
      draft.isLoading = true;
      draft.error = null;
      draft.activeStep++;
      break;
    case actionCreator('req', types.PREVIOUS_STEP):
      draft.isLoading = true;
      draft.error = null;
      draft.activeStep--;
      break;
    case actionCreator('req', types.GET_BRANCHES):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('res', types.GET_BRANCHES):
      draft.isLoading = false;
      draft.error = null;
      draft.branches = action.payload;
      break;
    case actionCreator('req', types.GET_ORDERS):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('res', types.GET_ORDERS):
      draft.isLoading = false;
      draft.error = null;
      draft.orders = action.payload;
      break;
    case actionCreator('req', types.STEP1):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('res', types.STEP1):
      draft.isLoading = false;
      draft.error = null;
      draft.checkToOrder.accountNumber = action.payload.accountNumber;
      break;
    case actionCreator('req', types.STEP2):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('res', types.STEP2):
      draft.isLoading = false;
      draft.error = null;
      draft.checkToOrder.nature = action.payload.nature;
      draft.checkToOrder.quantity = action.payload.quantity;
      break;
    case actionCreator('req', types.STEP3):
      draft.isLoading = true;
      draft.error = null;
      break;
    case actionCreator('res', types.STEP3):
      draft.isLoading = false;
      draft.error = null;
      draft.checkToOrder.branch = action.payload.branch;
      draft.activeStep = 1;
      break;
    case actionCreator('fail', types.GET_BRANCHES):
    case actionCreator('fail', types.GET_ORDERS):
    case actionCreator('fail', types.STEP1):
    case actionCreator('fail', types.STEP2):
    case actionCreator('fail', types.STEP3):
      draft.isLoading = true;
      draft.error = null;
      break;
  }
}, defaultState);
export default checksReducer;
