import { put, takeLatest, call, all } from 'redux-saga/effects';
import AS from '../../services/apis/as';
import { actionCreator } from '../../utils/generators';
import { sagaWithLoader } from '../../redux/template/sagaWithLoader';
import types from './menu.types';

function* updatePassword(updatePasswordAction) {
  try {
    const response = yield call(
      new AS().updatePassword,
      updatePasswordAction.payload,
    );
    yield all([
      put({
        type: actionCreator('res', types.UPDATE_PASSWORD_ACTION),
        payload: response,
      }),
      put({
        type: actionCreator('req', types.TOGGLE_UPDATE_PASSWORD_MODAL),
        payload: { success: true },
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.UPDATE_PASSWORD_ACTION),
      payload: error.payload.text,
    });
  }
}
export function* menuSagas() {
  yield takeLatest(
    actionCreator('req', types.UPDATE_PASSWORD_ACTION),
    sagaWithLoader,
    updatePassword,
  );
}
