import AxiosAPI from '../../rest/AxiosAPI';
import Config from '../../rest/Config';
import { ErrorHandler } from './ErrorHandler';

export default class Cards {
  _handleOnSuccess(response, resolver) {
    return resolver({ data: response.data, status: response.status });
  }
  _handleOnError(error, reject) {
    return reject(ErrorHandler(error));
  }
  getCards = () =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.CARDS)
        .sendGet()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  getCardTransactions = cardNumber =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.TRANSACTIONS)
        .setQueryParams([{ key: 'cardNumber', value: cardNumber }])
        .sendGet()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  getCardLimits = (cardNumber, productCode) =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.LIMITS)
        .setQueryParams([
          { key: 'cardNumber', value: cardNumber },
          { key: 'productCode', value: productCode },
        ])
        .sendGet()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  editLimit = (newLimit, cardNumber, productCode) =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.EDIT_LIMIT)
        .setQueryParams([
          { key: 'cardNumber', value: cardNumber },
          { key: 'productCode', value: productCode },
        ])
        .setData(newLimit)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  activateCard = cardNumber =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.ACTIVATE)
        .setQueryParams([{ key: 'cardNumber', value: cardNumber }])
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  deactivateCard = cardNumber =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.DEACTIVATE)
        .setQueryParams([{ key: 'cardNumber', value: cardNumber }])
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  opposeCard = cardNumber =>
    new Promise((resolver, reject) =>
      new AxiosAPI(true)
        .setUrl(Config.Cards.OPPOSE)
        .setQueryParams([{ key: 'cardNumber', value: cardNumber }])
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
}
