import React from 'react';
import i18n from '../../i18n';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import LanguageIcon from '../../assets/SvgComponents/LanguageIcon/LanguageIcon';

const ChangeLanguageButton = props => {
  const changeLanguage = lng => {
    i18n.changeLanguage(lng);
  };

  return (
    <div className="language-bloc">
      <LanguageIcon />
      {props.topHeader ? (
        <label>{t(`Components:${i18n.language}`)}</label>
      ) : (
        <label>{t(`Components:${i18n.language}`)}</label>
      )}
      <ul className="language-switcher-locale-url">
        <li
          className="fr"
          onClick={() => {
            changeLanguage('fr');
            localStorage.setItem('lng', 'fr');
          }}
        >
          <a className="language-link">{t('Components:fr')}</a>
        </li>
        <li
          className="en"
          onClick={() => {
            changeLanguage('en');
            localStorage.setItem('lng', 'en');
          }}
        >
          <a className="language-link"> {t('Components:en')}</a>
        </li>
      </ul>
    </div>
  );
};
export default withTranslation()(ChangeLanguageButton);
