import types from './transfer.types';
import produce from 'immer';
import { actionCreator } from '../../utils/generators';

const defaultState = {
  isLoading: false,
  error: null,
  history: [],
  activeStep: 0,
};

const TransferReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.RESET_STEPPER):
      draft.activeStep = 0;
      break;
    case actionCreator('req', types.SET_TO_DEFAULT_STATE):
      draft.activeStep = 0;
      draft.isLoading= false;
      draft.error= null;
      draft.history= [];
      draft.recapType= "PASSWORD";  
      break;
    case actionCreator('req', types.INIT_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.INIT_TRANSFER):
      draft.error = null;
      draft.isLoading = false;
      draft.otpId = action.payload.otpId;
      draft.virementId = action.payload.virementId;
      draft.recapType = action.payload.type;
      draft.activeStep = 2;
      break;
    case actionCreator('fail', types.INIT_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      draft.activeStep = 1;
      break;
      case actionCreator('req', types.INIT_C_TO_C_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
      case actionCreator('res', types.INIT_C_TO_C_TRANSFER):
      draft.error = null;
      draft.isLoading = false;
      draft.otpId = action.payload.otpId;
      draft.virementId = action.payload.virementId;
      draft.recapType = action.payload.type;
      draft.virData = action.payload;
      draft.activeStep = 3;
      break;
      case actionCreator('fail', types.INIT_C_TO_C_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      draft.activeStep = 1;
      break;
    case actionCreator('req', types.VALIDATE_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.VALIDATE_TRANSFER):
      draft.error = null;
      draft.isLoading = false;
      draft.virData = action.payload.data;
      draft.activeStep = 3;
      break;
    case actionCreator('fail', types.VALIDATE_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      draft.activeStep = 2;
      break;
    case actionCreator('req', types.PASSWORD_VALIDATE_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.PASSWORD_VALIDATE_TRANSFER):
      draft.error = null;
      draft.isLoading = false;
      draft.virData = action.payload.data;
      draft.activeStep = 3;
      break;
    case actionCreator('fail', types.PASSWORD_VALIDATE_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      draft.activeStep = 2;
      break;
    case actionCreator('req', types.RESEND_OTP_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.RESEND_OTP_TRANSFER):
      draft.otpId = action.payload.data.otpId;
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', types.RESEND_OTP_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    case actionCreator('req', types.GET_HISTORY_TRANSFER):
      draft.error = null;
      draft.isLoading = true;
      break;
    case actionCreator('res', types.GET_HISTORY_TRANSFER):
      draft.error = null;
      draft.isLoading = false;
      draft.history = action.payload.data;
      break;
    case actionCreator('fail', types.GET_HISTORY_TRANSFER):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
  }
}, defaultState);

export default TransferReducer;
