//@flow
import * as React from 'react';
import CardTitle from '../SideBarCard/CardTitle/CardTitle';
import SideBarDashboardSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarDashboardSvg';
import SideBarAccountsSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarAccountsSvg';
import SideBarCardsChecksSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarCardsChecksSvg';
import SideBarSimulatorSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarSimulatorSvg';
import SideBarTransfersSvg from '../../../../assets/SvgComponents/SideBarSvg/SideBarTransfersSvg';
import CardElements from '../SideBarCard/CardElements/CardElements';
import ItemSub from '../SubElements/ItemSub';
import CardItems from '../SideBarCard/CardItems/CardItems';
type Props = {
  key?: string,
  isNode: boolean,
  title: string,
  link?: string,
  onClick?: Function,
  icon?: string,
  order: number,
  subMenus: Array<any>,
  current: Boolean,
};
const icons = {
  dashboard: SideBarDashboardSvg,
  accounts: SideBarAccountsSvg,
  cards: SideBarCardsChecksSvg,
  transfers: SideBarTransfersSvg,
  simulation: SideBarSimulatorSvg,
};
const subItems = menu => (
  <MenuItem
    isNode={menu.isNode}
    icon={menu.icon}
    title={menu.title}
    link={menu.link}
    subMenus={menu.subMenus}
    order={menu.order}
    onClick={menu.onClick}
    key={menu.key}
    current={menu.current}
    authorization={menu.authorization}
  />
);
const FirstOrderMenuItem = (props: Props) => {
  const IconTag = props.icon ? icons[props.icon] : null;
  return (
    <li
      className={props.current ? 'current' : ''}
      key={props.key}
      onClick={props.onClick}
    >
      <CardTitle title={props.title} link={props.link}>
        {IconTag && <IconTag />}
      </CardTitle>
      {props.isNode && props.subMenus.map(menu => subItems(menu))}
    </li>
  );
};
const SecondOrderMenuItem = (props: Props) => (
  <CardElements subtitle={props.title} key={props.key}>
    {props.isNode && props.subMenus.map(menu => subItems(menu))}
  </CardElements>
);
const ThirdOrderMenuItem = (props: Props) => (
  <CardItems
    subtitle={props.title}
    link={props.link}
    class="submenu-accordion_title"
  >
    {props.isNode && props.subMenus.map(menu => subItems(menu))}
  </CardItems>
);
const unauthorizedStyle = {
  color: '#b7b7b7',
  cursor: 'no-drop',
};
const LeafMenuItem = (props: Props) => (
  <ItemSub
    text={props.title}
    link={props.authorization ? props.link : ''}
    onClick={props.onClick}
    style={props.authorization ? null : unauthorizedStyle}
    key={props.key}
  />
);
const MenuItem = (props: Props) => (
  <>
    {props.order === 1 && <FirstOrderMenuItem {...props} />}
    {props.order === 2 && <SecondOrderMenuItem {...props} />}
    {props.order === 3 && <ThirdOrderMenuItem {...props} />}
    {props.order === 4 && <LeafMenuItem {...props} />}
  </>
);
MenuItem.defaultProps = { subMenus: [] };
export default MenuItem;
