import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../../../../redux/login/login.actions';
import { headerDropdowns } from '../HeaderItems';
import { t } from '../../../../i18n';
import { withTranslation } from 'react-i18next';
import ProfileMenu from './ItemsMenus/ProfileMenu';
import BackDrop from '../../../Ui/BackDrop/BackDrop';

class HeaderItem extends React.PureComponent {
  state = { showDropdown: false };
  _renderMenu = menu => {
    if (this.state.showDropdown) {
      switch (menu) {
        case headerDropdowns.PROFILE:
          return <ProfileMenu />;
        case headerDropdowns.LOGOUT:
          return this.props.logout();
        default:
          return null;
      }
    }
    return null;
  };
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.shownMenu !== this.props.shownMenu) {
      this.setState({ showDropdown: this.props.shownMenu === this.props.type });
    }
  }
  render() {
    const itemClass = `dropdown ${this.state.showDropdown ? 'open' : ''}`;
    return (
      <>
        <li className={itemClass} onClick={this.props.showMenu}>
          <Link className="bcp-toggle-right-sidebar">
            <div>
              <span
                className={`icon${
                  this.props.type === headerDropdowns.LOGOUT
                    ? ' icon-logout'
                    : null
                }`}
              >
                {this.props.children}
              </span>
              <span>
                {this.props.type !== headerDropdowns.LOGOUT
                  ? t(this.props.type)
                  : null}
              </span>
            </div>
          </Link>
          {this._renderMenu(this.props.shownMenu)}
        </li>
        <BackDrop
          show={this.state.showDropdown}
          clicked={this.props.hideMenu}
          style={{ 'background-color': 'transparent', 'z-index': 0 }}
        />
      </>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  logout: () => {
    dispatch(logout());
  },
});

export default withTranslation()(
  connect(
    null,
    mapDispatchToProps,
  )(HeaderItem),
);
