function* getHistory() {
  try {
    const history = yield call(new Transfer().getHistory);
    yield put({
      type: actionCreator('res', types.GET_HISTORY_TRANSFER),
      payload: history,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_HISTORY_TRANSFER),
      }),
    ]);
  }
}
export function* transferSagas() {
  yield takeLatest(
    actionCreator('req', types.INIT_TRANSFER),
    sagaWithLoader,
    init,
  );
  yield takeLatest(
    actionCreator('req', types.INIT_C_TO_C_TRANSFER),
    sagaWithLoader,
    initCToC,
  );
  yield takeLatest(
    actionCreator('req', types.VALIDATE_TRANSFER),
    sagaWithLoader,
    validate,
  );
  yield takeLatest(
    actionCreator('req', types.PASSWORD_VALIDATE_TRANSFER),
    sagaWithLoader,
    passwordValidate,
  );
  yield takeLatest(
    actionCreator('req', types.RESEND_OTP_TRANSFER),
    sagaWithLoader,
    resendOtp,
  );
  yield takeLatest(
    actionCreator('req', types.GET_HISTORY_TRANSFER),
    sagaWithLoader,
    getHistory,
  );
}
