import React, { Component } from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import * as Yup from 'yup';
import { t } from 'i18n';
import { withTranslation } from 'react-i18next';
import FormModal from '../../../../Ui/CustomModal/FormModal';
import { updatePassword } from '../../../../../redux/menu/menu.actions';
import ExpiringFormFieldError from '../../../../Ui/FormFieldError/ExpiringFormFieldError';
import classes from '../HeaderItem.module.css';
import Alert from '../../../../Ui/Alert/Alert';

const validationSchema = Yup.object().shape({
  oldPassword: Yup.string().required(t('Components:requiredField')),
  newPassword: Yup.string()
    .required(t('Components:requiredField'))
    .min(8, t(`Components:passwordMinLengthMsg`))
    .max(12, t(`Components:passwordMaxLengthMsg`))
    .matches(/^(?=.*[a-z]).*$/, t(`Components:invalidPasswordLowerCase`))
    .matches(/^(?=.*[A-Z]).*$/, t(`Components:invalidPasswordUpperCase`))
    .matches(/^(?=.*\d).*$/, t(`Components:invalidPasswordNumber`))
    .matches(
      /^(?=.*[@$!%*?&]).*$/,
      t(`Components:invalidPasswordSpecialCharacter`),
    ),
  confirmNewPassword: Yup.string()
    .required(t('Components:requiredField'))
    .test(
      'confirm password validation',
      t(`Components:passwordConfirmationIncorrect`),
      function(value) {
        const { newPassword } = this.parent;
        return newPassword === value;
      },
    ),
});

const invalidInputClasses = `form-control col-sm-12 col-md-8 ${classes.Invalid}`;

class UpdatePasswordModal extends Component {
  constructor(props) {
    super(props);
    this.formModalRef = null;
  }
  state = {
    showAlert: false,
    showPasswords: false,
  };
  _showPasswords = () => {
    this.setState({
      showPasswords: true,
    });
  };
  _hidePasswords = () => {
    this.setState({ showPasswords: false });
  };
  _setFormModalRef = ref => {
    this.formModalRef = ref;
  };
  _hideAlert = () => this.setState({ showAlert: false });
  _renderAlert = () => {
    if (this.state.showAlert) {
      return (
        <Alert
          close={this._hideAlert}
          type="Default Danger"
          text={this.props.error}
          className="animated"
        />
      );
    }
  };
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.error !== this.props.error && this.props.error) {
      this.setState({ showAlert: true });
    }
  }
  componentDidMount() {
    document.addEventListener('mouseup', this._hidePasswords);
  }
  componentWillUnmount() {
    document.removeEventListener('mouseup', this._hidePasswords);
  }
  render() {
    return (
      <>
        <FormModal
          title={t('Components:updatingPassword')}
          show={this.props.show}
          handleClose={this.props.handleClose}
          hasValidateBtn
          handleValidate={() => this.formModalRef.submitForm()}
          validateTitle={t('Components:validate')}
          alert={this._renderAlert}
        >
          <Formik
            ref={ref => this._setFormModalRef(ref)}
            validationSchema={validationSchema}
            onSubmit={(values, { setSubmitting }) => {
              setSubmitting(true);
              this.props.updatePassword(values.oldPassword, values.newPassword);
            }}
          >
            {({ values, errors, handleChange, handleBlur, handleSubmit }) => (
              <form onSubmit={handleSubmit}>
                <div className="modal-body">
                  <div className="form-group">
                    <label
                      className="col-sm-12 col-md-4 control-label"
                      htmlFor="oldPassword"
                    >
                      {t(`Components:oldPassword`)}
                    </label>
                    <input
                      className={
                        errors.oldPassword
                          ? invalidInputClasses
                          : 'form-control col-sm-12 col-md-8'
                      }
                      name="oldPassword"
                      type={this.state.showPasswords ? 'text' : 'password'}
                      id="oldPassword"
                      placeholder={t('Components:oldPasswordPlaceholder')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.oldPassword}
                    />
                    <span className="input-show-btn">
                      <button
                        className="btn reveal"
                        type="button"
                        onMouseDown={this._showPasswords}
                        onTouchStart={this._showPasswords}
                        onTouchEnd={this._hidePasswords}
                      />
                    </span>
                    <ExpiringFormFieldError name="oldPassword" />
                  </div>
                  <div className="form-group">
                    <label
                      className="col-sm-12 col-md-4 control-label"
                      htmlFor="newPassword"
                    >
                      {t(`Components:newPassword`)}
                      <sup>*</sup>
                    </label>
                    <input
                      className={
                        errors.newPassword
                          ? invalidInputClasses
                          : 'form-control col-sm-12 col-md-8'
                      }
                      name="newPassword"
                      type={this.state.showPasswords ? 'text' : 'password'}
                      id="newPassword"
                      placeholder={t('Components:newPasswordPlaceholder')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.newPassword}
                    />
                    <ExpiringFormFieldError name="newPassword" />
                  </div>
                  <div className="form-group">
                    <label
                      className="col-sm-12 col-md-4 control-label"
                      htmlFor="confirmationNewPassword"
                    >
                      {t(`Components:newPasswordConfirmation`)}
                    </label>
                    <input
                      className={
                        errors.confirmNewPassword
                          ? invalidInputClasses
                          : 'form-control col-sm-12 col-md-8'
                      }
                      name="confirmNewPassword"
                      type={this.state.showPasswords ? 'text' : 'password'}
                      id="confirmNewPassword"
                      placeholder={t(
                        'Components:confirmationNewPasswordPlaceholder',
                      )}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.confirmNewPassword}
                    />
                    <ExpiringFormFieldError name="confirmNewPassword" />
                  </div>
                </div>
              </form>
            )}
          </Formik>
          <p className="champs_obligatoires">
            <sup>(*)</sup>
            {t(`Components:passwordForm`)}
          </p>
        </FormModal>
      </>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  updatePassword: (oldPassword, newPassword) => {
    dispatch(updatePassword({ oldPassword, newPassword }));
  },
});
const mapStateToProps = state => ({
  error: state.Menu.error,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(UpdatePasswordModal),
);
