import { put, takeLatest, call, all } from 'redux-saga/effects';
import Public from '../../services/apis/public';
import { actionCreator } from '../../utils/generators';
import types from './localisations.types';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* getGabs() {
  try {
    const Gabs = yield call(new Public().getGabs);
    yield put({
      type: actionCreator('res', types.GET_GAB_LOCALISATIONS),
      payload: Gabs,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_GAB_LOCALISATIONS),
      }),
    ]);
  }
}
function* getBranches() {
  try {
    const Branches = yield call(new Public().getBranches);
    yield put({
      type: actionCreator('res', types.GET_BRANCHES_LOCALISATIONS),
      payload: Branches,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_BRANCHES_LOCALISATIONS),
      }),
    ]);
  }
}

export function* localisationsSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_GAB_LOCALISATIONS),
    sagaWithLoader,
    getGabs,
  );
  yield takeLatest(
    actionCreator('req', types.GET_BRANCHES_LOCALISATIONS),
    sagaWithLoader,
    getBranches,
  );
}
