import { put, takeLatest, call, all } from 'redux-saga/effects';
import Accounts from '../../services/apis/accounts';
import { actionCreator } from '../../utils/generators';
import types from './accounts.types';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* getAccounts() {
  try {
    const accounts = yield call(new Accounts().getAccounts);
    yield put({
      type: actionCreator('res', types.GET_ACCOUNTS),
      payload: accounts,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_ACCOUNTS),
      }),
    ]);
  }
}

function* getSignaletic() {
  try {
    const signaletic = yield call(new Accounts().getSignaletic);
    yield put({
      type: actionCreator('res', types.GET_SIGNALETIC),
      payload: signaletic,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_SIGNALETIC),
      }),
    ]);
  }
}

export function* accountsSagas() {
  yield takeLatest(actionCreator('req', types.GET_ACCOUNTS), getAccounts);
  yield takeLatest(
    actionCreator('req', types.GET_SIGNALETIC),
    sagaWithLoader,
    getSignaletic,
  );
}
