import { all, call } from 'redux-saga/effects';
import { accountsSagas } from './accounts/accounts.sagas';
import { operationsSagas } from './operations/operations.sagas';
import { loginSagas } from './login/login.sagas';
import { enrollmentSagas } from './enrollment/enrollment.sagas';
import { beneficiariesSagas } from './beneficiaries/beneficiaries.sagas';
import { transferSagas } from './transfer/transfer.sagas';
import { cashTransferSagas } from './cashTransfer/cashTransfer.sagas';
import { cardsSagas } from './cards/cards.sagas';
import { favoritesSagas } from './favorites/favorites.sagas';
import { menuSagas } from './menu/menu.sagas';
import { localisationsSagas } from './geoLocalisation/localisations.sagas';
import { checksSagas } from './checks/checks.sagas';

export default function* rootSaga() {
  yield all([
    call(accountsSagas),
    call(operationsSagas),
    call(loginSagas),
    call(enrollmentSagas),
    call(beneficiariesSagas),
    call(transferSagas),
    call(cashTransferSagas),
    call(cardsSagas),
    call(checksSagas),
    call(favoritesSagas),
    call(menuSagas),
    call(localisationsSagas),
  ]);
}
