//@flow
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import MenuItem from '../SideBarItems/MenuItem/MenuItem';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import {
  getAccounts,
  selectAccount,
} from '../../../redux/accounts/accounts.actions';
import { ConnectedLayoutConsumer } from '../../ConnectedLayout/ConnectedLayout';
import {
  activateSimulatorTab,
  activateAccountsTab,
  activateTransfersTab,
  activateCardsTab,
  activateTabWithParams,
  activateChecksTab,
} from '../../../redux/menu/menu.actions';
import SideBarLogoSvg from '../../../assets/SvgComponents/SideBarSvg/SideBarLogoSvg';
import { showMessage } from '../../../redux/messages/messages.actions';
import history from '../../../utils/history';
type Props = {
  accounts: [],
  getAccounts: Function,
  selectAccount: Function,
  activateTab: Function,
};
const MENU_NAMESPACE = 'Components:menu.';
const SideMenuContainer = styled.div.attrs(props => ({
  className: `bcp-left-sidebar ${props.showMenu ? '' : 'hidden-sidebar'}`,
}))``;
let services = {
  BENEFICIARY_READ: false,
  ACCOUNT_TRANSFER: false,
  ACCOUNT_READ: false,
  LANDING_PAGE: false,
  CARD_READ: false,
  ATM_TRANSFERT: false,
};
class SideMenu extends Component<Props> {
  state = {
    activeItemKey: null,
    currentLink: null,
  };
  _authorizationBanedHandler = () => {
    this.props.showMessage(
      t('Errors:authorizationMessage'),
      'ERROR',
      t('Components:validate'),
      false,
    );
  };
  _setActiveItemKey = itemKey => {
    this.setState({ activeItemKey: itemKey, currentLink: null });
  };
  _handleSelectedAccount = (accountId, activeTab) => {
    this.props.selectAccount(accountId);
    this.props.activateAccountsTab(activeTab);
  };
  _buildAccountSubMenu = toggleMenu =>
    this.props.accounts.map(account => ({
      key: account.clientAccountId,
      isNode: true,
      title: t(`${MENU_NAMESPACE}account`, { account: account.accountNumber }),
      order: 3,
      subMenus: [
        {
          key: `${account.clientAccountId}1`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}operations`),
          link: '/accounts',
          order: 4,
          authorization: services.ACCOUNT_READ,
          onClick: () => {
            if (services.ACCOUNT_READ) {
              this._handleSelectedAccount(
                account.clientAccountId,
                'operations',
              );
              this._setActiveItemKey(`accounts${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `${account.clientAccountId}2`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}signaletiqFile`),
          link: '/accounts',
          order: 4,
          authorization: services.ACCOUNT_READ,
          onClick: () => {
            if (services.ACCOUNT_READ) {
              this._handleSelectedAccount(account.clientAccountId, 'details');
              this._setActiveItemKey(`accounts${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `${account.clientAccountId}3`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}rib`),
          link: '/accounts',
          order: 4,
          authorization: services.ACCOUNT_READ,
          onClick: () => {
            if (services.ACCOUNT_READ) {
              this._handleSelectedAccount(account.clientAccountId, 'rib');
              this._setActiveItemKey(`accounts${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
      ],
    }));
  _buildCardsSubMenu = toggleMenu => [
    {
      key: `cards${1}`,
      isNode: true,
      title: t(`${MENU_NAMESPACE}cards`),
      order: 3,
      subMenus: [
        {
          key: `cardsDetails${1}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}cardsDetails`),
          order: 4,
          authorization: services.CARD_READ,
          link: '/cards',
          onClick: () => {
            if (services.CARD_READ) {
              this.props.activateCardsTab('cardsDetails');
              this._setActiveItemKey(`cardsAndChecks${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
      ],
    },
    // {
    //   key: `checks${2}`,
    //   isNode: true,
    //   title: t(`${MENU_NAMESPACE}checks`),
    //   order: 3,
    //   subMenus: [
    //     {
    //       key: `checkBooks${1}`,
    //       isNode: false,
    //       title: t(`${MENU_NAMESPACE}checkBooks`),
    //       order: 4,
    //       authorization: services.CARD_READ,
    //       link: '/checks',
    //       onClick: () => {
    //         if (services.CARD_READ) {
    //           this.props.activateChecksTab('checkBooks');
    //           this._setActiveItemKey(`checkBooks${1}`);
    //         } else {
    //           this._authorizationBanedHandler();
    //         }
    //       },
    //     },
    //     {
    //       key: `orderHistory${1}`,
    //       isNode: false,
    //       title: t(`${MENU_NAMESPACE}orderHistory`),
    //       order: 4,
    //       authorization: services.CARD_READ,
    //       link: '/checks',
    //       onClick: () => {
    //         if (services.CARD_READ) {
    //           this.props.activateChecksTab('ordersHistory');
    //           this._setActiveItemKey(`orderHistory${1}`);
    //         } else {
    //           this._authorizationBanedHandler();
    //         }
    //       },
    //     },
    //   ],
    // },
  ];
  _buildSimulationSubMenu = toggleMenu => [
    {
      key: `payments${3}`,
      isNode: true,
      title: t(`${MENU_NAMESPACE}simulators`),
      order: 3,
      subMenus: [
        // {
        //   key: `mortgage${4}`,
        //   isNode: false,
        //   title: t(`${MENU_NAMESPACE}mortgage`),
        //   order: 4,
        //   link: '/simulator',
        //   authorization: true,
        //   onClick: () => {
        //     this.props.activateSimulatorTab('mortgage');
        //     this._setActiveItemKey(`simulators${1}`);
        //   },
        // },
        {
          key: `consumerLoan${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}consumerLoan`),
          order: 4,
          link: '/simulator',
          authorization: true,
          onClick: () => {
            this.props.activateSimulatorTab('consumerLoan');
            this._setActiveItemKey(`simulators${1}`);
            if (typeof window.orientation !== 'undefined') {
              toggleMenu('');
            }
          },
        },
      ],
    },
  ];
  _buildTransfersSubMenu = toggleMenu => [
    //menu commented out of scope
    /*{
      key: `payments${3}`,
      isNode: true,
      title: t(`${MENU_NAMESPACE}payments`),
      order: 3,
      subMenus: [
        {
          key: `favorites${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}favorites`),
          order: 4,
          link: '/',
        },
        {
          key: `merchantsList${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}merchantsList`),
          order: 4,
          link: '/',
        },
        {
          key: `phoneCredit${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}phoneCredit`),
          order: 4,
          link: '/',
        },
        {
          key: `phoneAndInternet${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}phoneAndInternet`),
          order: 4,
          link: '/',
        },
        {
          key: `waterAndElectricity${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}waterAndElectricity`),
          order: 4,
          link: '/',
        },
        {
          key: `transport${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}transport`),
          order: 4,
          link: '/',
        },
        {
          key: `taxesAndAdministrations${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}taxesAndAdministrations`),
          order: 4,
          link: '/',
        },
        {
          key: `schoolsAndUniversities${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}schoolsAndUniversities`),
          order: 4,
          link: '/',
        },
        {
          key: `airlines${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}airlines`),
          order: 4,
          link: '/',
        },
        {
          key: `stickerPayment${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}stickerPayment`),
          order: 4,
          link: '/',
        },
        {
          key: `history${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}history`),
          order: 4,
          link: '/',
        },
      ],
    },*/
    {
      key: `bankTransfers${3}`,
      isNode: true,
      title: t(`${MENU_NAMESPACE}bankTransfers`),
      order: 3,
      subMenus: [
        {
          key: `favorites${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}favorites`),
          order: 4,
          link: '/transfers',
          authorization: services.ACCOUNT_TRANSFER,
          onClick: () => {
            if (services.ACCOUNT_TRANSFER) {
              this.props.activateTransfersTab('favorites');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `bankTransfer${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}bankTransfer`),
          order: 4,
          link: '/transfers',
          authorization: services.ACCOUNT_TRANSFER,
          onClick: () => {
            if (services.ACCOUNT_TRANSFER) {
              this.props.activateTransfersTab('transfer');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `history${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}history`),
          order: 4,
          link: '/transfers',
          authorization: services.ACCOUNT_TRANSFER,
          onClick: () => {
            if (services.ACCOUNT_TRANSFER) {
              this.props.activateTransfersTab('history');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `beneficiaries${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}beneficiaries`),
          order: 4,
          link: '/transfers',
          authorization: services.BENEFICIARY_READ,
          onClick: () => {
            if (services.BENEFICIARY_READ) {
              this.props.activateTransfersTab('beneficiaries');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
      ],
    },
    {
      key: `cashTransfers${3}`,
      isNode: true,
      title: t(`${MENU_NAMESPACE}cashTransfers`),
      order: 3,
      subMenus: [
        {
          key: `favorites${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}favorites`),
          order: 4,
          link: '/transfers',
          authorization: services.ATM_TRANSFERT,
          onClick: () => {
            if (services.ATM_TRANSFERT) {
              this.props.activateTransfersTab('favorites');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `cashTransfer${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}cashTransfer`),
          order: 4,
          link: '/transfers',
          authorization: services.ATM_TRANSFERT,
          onClick: () => {
            if (services.ATM_TRANSFERT) {
              this.props.activateTransfersTab('cashTransfer');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `history${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}history`),
          order: 4,
          link: '/transfers',
          authorization: services.ATM_TRANSFERT,
          onClick: () => {
            if (services.ATM_TRANSFERT) {
              this.props.activateTransfersTab('history');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
        {
          key: `beneficiaries${4}`,
          isNode: false,
          title: t(`${MENU_NAMESPACE}beneficiaries`),
          order: 4,
          link: '/transfers',
          authorization: services.BENEFICIARY_READ,
          onClick: () => {
            if (services.BENEFICIARY_READ) {
              this.props.activateTransfersTab('beneficiaries');
              this._setActiveItemKey(`paymentsAndTransfers${1}`);
              if (typeof window.orientation !== 'undefined') {
                toggleMenu('');
              }
            } else {
              this._authorizationBanedHandler();
            }
          },
        },
      ],
    },
  ];
  _buildSideMenu = toggleMenu => {
    const menuItems = [
      {
        key: `dashboard${1}`,
        isNode: false,
        title: t(`${MENU_NAMESPACE}dashboard`),
        link: '/dashboard',
        path: '/dashboard',
        icon: 'dashboard',
        order: 1,
        subMenus: [],
        onClick: () => this._setActiveItemKey(`dashboard${1}`),
      },
      {
        key: `accounts${1}`,
        isNode: true,
        title: t(`${MENU_NAMESPACE}accounts`),
        link: '#',
        path: '/accounts',
        icon: 'accounts',
        order: 1,
        subMenus: [
          {
            isNode: true,
            key: `accounts${2}`,
            title: t(`${MENU_NAMESPACE}accounts`),
            order: 2,
            subMenus: this._buildAccountSubMenu(toggleMenu),
          },
        ],
      },
      {
        key: `cardsAndChecks${1}`,
        isNode: true,
        title: t(`${MENU_NAMESPACE}cardsAndChecks`),
        link: '#',
        path: '/cards',
        icon: 'cards',
        order: 1,
        subMenus: [
          {
            isNode: true,
            key: `cardsAndChecks${2}`,
            title: t(`${MENU_NAMESPACE}cardsAndChecks`),
            order: 2,
            subMenus: this._buildCardsSubMenu(toggleMenu),
          },
        ],
      },
      {
        key: `paymentsAndTransfers${1}`,
        isNode: true,
        title: t(`${MENU_NAMESPACE}paymentsAndTransfers`),
        link: '#',
        path: '/transfers',
        icon: 'transfers',
        order: 1,
        subMenus: [
          {
            isNode: true,
            key: `paymentsAndTransfers${2}`,
            title: t(`${MENU_NAMESPACE}paymentsAndTransfers`),
            order: 2,
            subMenus: this._buildTransfersSubMenu(toggleMenu),
          },
        ],
      },
      {
        key: `simulators${1}`,
        isNode: true,
        title: t(`${MENU_NAMESPACE}simulators`),
        link: '#',
        path: '/simulator',
        icon: 'simulation',
        order: 1,
        subMenus: [
          {
            isNode: true,
            key: `simulators${2}`,
            title: t(`${MENU_NAMESPACE}simulators`),
            order: 2,
            subMenus: this._buildSimulationSubMenu(toggleMenu),
          },
        ],
      },
    ];
    return menuItems;
  };
  componentDidMount() {
    if (this.props.accounts.length === 0) this.props.getAccounts();
    services = sessionStorage.getItem('services')
      ? JSON.parse(sessionStorage.getItem('services'))
      : services;
    this.setState({ currentLink: history.location.pathname });
  }
  render() {
    return (
      <ConnectedLayoutConsumer>
        {({ showMenu, toggleMenu }) => (
          <SideMenuContainer showMenu={showMenu}>
            <div className="logo-left">
              <Link className="navbar-brand" to="/dashboard">
                <SideBarLogoSvg />
              </Link>
            </div>
            <div className="left-sidebar-wrapper">
              <div className="left-sidebar-spacer">
                <div className="left-sidebar-scroll">
                  <div className="left-sidebar-content">
                    <div className="nav-principal">
                      <span
                        className="close-menu"
                        onClick={() => {
                          toggleMenu('');
                        }}
                      >
                        Close X
                      </span>
                      <div className="nav__global">
                        <div className="nav__inner">
                          <nav>
                            <ul className="sidebar-elements">
                              {this._buildSideMenu(toggleMenu).map(menu => (
                                <MenuItem
                                  isNode={menu.isNode}
                                  icon={menu.icon}
                                  title={menu.title}
                                  link={menu.link}
                                  subMenus={menu.subMenus}
                                  order={menu.order}
                                  onClick={menu.onClick}
                                  key={menu.key}
                                  current={
                                    menu.path === this.state.currentLink ||
                                    menu.key === this.state.activeItemKey
                                  }
                                  authorization={menu.authorization}
                                />
                              ))}
                            </ul>
                          </nav>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </SideMenuContainer>
        )}
      </ConnectedLayoutConsumer>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getAccounts: () => {
    dispatch(getAccounts());
  },
  selectAccount: selectedAccount => {
    dispatch(selectAccount(selectedAccount));
  },
  activateAccountsTab: activeTab => {
    dispatch(activateAccountsTab(activeTab));
  },
  activateTransfersTab: activeTab => {
    dispatch(activateTransfersTab(activeTab));
  },
  activateSimulatorTab: activeTab => {
    dispatch(activateSimulatorTab(activeTab));
  },
  activateCardsTab: activeTab => {
    dispatch(activateCardsTab(activeTab));
  },
  activateChecksTab: activeTab => {
    dispatch(activateChecksTab(activeTab));
  },
  activateTabWithParams: activateTabRequest => {
    dispatch(activateTabWithParams(activateTabRequest));
  },
  showMessage: (text, severity, validateTitle, hasCancelBtn) => {
    dispatch(showMessage(text, severity, validateTitle, hasCancelBtn));
  },
});

const mapStateToProps = state => ({
  accounts: state.Accounts.accounts,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(SideMenu),
);
