import { actionCreator } from '../../utils/generators';
import types from './cards.types';
import produce from 'immer';
import { formatDateTime } from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';

const defaultState = {
  isLoading: false,
  error: null,
  cards: null,
  cardTransactions: [],
  cardLimits: { periodicityCode: '', limits: [] },
  selectedCardNumber: '',
  activeTab: 'cardsDetails',
};

const cardsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.GET_CARDS):
    case actionCreator('req', types.SWITCH_STATE):
    case actionCreator('req', types.OPPOSE):
    case actionCreator('req', types.GET_TRANSACTIONS):
    case actionCreator('req', types.GET_LIMITS):
    case actionCreator('req', types.EDIT_LIMIT):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', types.GET_CARDS):
      draft.error = null;
      draft.loading = false;
      draft.cards = action.payload.map(card => ({
        ...card,
        isActive: card.activationFlag === 'Y',
        isOpposed: card.flagOpposition === 'Y',
        ownerName: `${card.familyName} ${card.firstName}`,
        expiryDate: formatDateTime(
          card.expiryDate,
          FormatsConfiguration.DateTimeFormat.fullDate,
        ),
        formattedExpiryDate: formatDateTime(
          card.expiryDate,
          FormatsConfiguration.DateTimeFormat.expiryDate,
        ),
        deliveryCardDate: formatDateTime(
          card.deliveryCardDate,
          FormatsConfiguration.DateTimeFormat.fullDate,
        ),
        validationStartDate: formatDateTime(
          card.startValDate,
          FormatsConfiguration.DateTimeFormat.fullDate,
        ),
      }));
      if (draft.cards.length > 0)
        draft.selectedCardNumber = draft.cards[0].cardNumber;
      break;
    case actionCreator('req', types.SELECT_CARD):
      draft.selectedCardNumber = action.payload;
      break;
    case actionCreator('res', types.GET_TRANSACTIONS):
      draft.cardTransactions = action.payload.data.slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('res', types.SWITCH_STATE):
      let switchedCardNumber = action.payload.data.numberCard;
      draft.cards.forEach((card, index) => {
        if (card.cardNumber === switchedCardNumber) {
          draft.cards[index].isActive = !draft.cards[index].isActive;
          draft.cards[index].activationFlag =
            draft.cards[index].activationFlag === 'Y' ? 'N' : 'Y';
        }
      });
      break;
    case actionCreator('res', types.OPPOSE):
      let opposedCardNumber = action.payload.data.cardNumber;
      draft.cards.forEach((card, index) => {
        if (card.cardNumber === opposedCardNumber) {
          draft.cards[index].isOpposed = false;
          draft.cards[index].flagOpposition = 'Y';
        }
      });
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('res', types.GET_LIMITS):
      draft.cardLimits = action.payload;
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('res', types.EDIT_LIMIT):
      draft.cardLimits.limits.forEach((limit, index) => {
        if (limit.processingCode === action.payload.processingCode) {
          draft.cardLimits.limits[index] = {
            ...draft.cardLimits.limits[index],
            ...action.payload,
          };
        }
      });
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', types.GET_CARDS):
    case actionCreator('fail', types.SWITCH_STATE):
    case actionCreator('fail', types.OPPOSE):
    case actionCreator('fail', types.GET_TRANSACTIONS):
      draft.cardTransactions = [];
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator('fail', types.GET_LIMITS):
      draft.cardLimits.limits = [];
      draft.cardLimits.periodicityCode = '';
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator('fail', types.EDIT_LIMIT):
      draft.error = 'error found';
      draft.loading = false;
      break;
  }
}, defaultState);
export default cardsReducer;
