import { put, takeLatest, call, all } from 'redux-saga/effects';
import { actionCreator } from '../../utils/generators';
import types from './favorites.types';
import { sagaWithLoader } from '../../redux/template/sagaWithLoader';
import Transfer from '../../services/apis/Transfer';
import CashTransfer from '../../services/apis/CashTransfer';

const transferTypes = { CASH_TRANSFER: 'cashTransfer', TRANSFER: 'transfer' };

function* getFavorites() {
  try {
    const [transferFavorites, cashTransferFavorites] = yield all([
      call(new Transfer().getFavorites),
      call(new CashTransfer().getFavorites),
    ]);
    if (transferFavorites.data.length > 0) {
      transferFavorites.data.forEach(transaction => {
        transaction.transactionId = transaction.accountTransferFavoriteId;
        transaction.transactionType = transferTypes.TRANSFER;
      });
    }
    if (cashTransferFavorites.data.length > 0) {
      cashTransferFavorites.data.forEach(transaction => {
        transaction.transactionId = transaction.atmTransferFavoriteId;
        transaction.transactionType = transferTypes.CASH_TRANSFER;
      });
    }
    yield put({
      type: actionCreator('res', types.GET_FAVORITES),
      payload: transferFavorites.data.concat(cashTransferFavorites.data),
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_FAVORITES),
      }),
    ]);
  }
}
function* deleteFavorite(deleteFavoriteAction) {
  const { transactionId, transactionType } = deleteFavoriteAction.payload;
  let transferService =
    transactionType === transferTypes.TRANSFER
      ? new Transfer()
      : new CashTransfer();
  try {
    yield call(transferService.deleteFavorite, transactionId);
    yield put({
      type: actionCreator('res', types.DELETE_FAVORITE),
      payload: deleteFavoriteAction.payload,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.DELETE_FAVORITE),
      }),
    ]);
  }
}
export function* favoritesSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_FAVORITES),
    sagaWithLoader,
    getFavorites,
  );
  yield takeLatest(
    actionCreator('req', types.DELETE_FAVORITE),
    sagaWithLoader,
    deleteFavorite,
  );
}
