import React from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { ENROLLMENT_STEP4_NAMESPACE } from '../constants';
import ResendOTPSVG from '../../../assets/SvgComponents/ResendOTPSVG/ResendOTPSVG';
import {
  retryStep3,
  step4,
} from '../../../redux/enrollment/enrollment.actions';

const EnrollmentStep4 = props => (
  <Formik
    ref={ref => props.formRefSetter(ref)}
    onSubmit={(values, { setSubmitting }) => {
      setSubmitting(true);
      props.step4({
        activityId: props.activityId,
        deviceId: 'web',
        hash: props.step3Hash,
        otp: values.otp,
        phone: props.phone,
      });
    }}
  >
    {({ handleSubmit, values, handleChange, handleBlur }) => (
      <form onSubmit={handleSubmit}>
        <div className="form-group col-md-12 col-sm-12">
          <div className="col-sm-6 col-md-6 margin-auto">
            <input
              name="otp"
              type="text"
              className="form-control"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.otp}
              placeholder={t(`${ENROLLMENT_STEP4_NAMESPACE}enterOTP`)}
            />
          </div>
          <div className="col-sm-12 col-md-12 renvoyer">
            <ResendOTPSVG />
            <span
              onClick={() =>
                props.resendOTP({
                  activityId: props.activityId,
                  deviceId: props.deviceId,
                  hash: props.step3Hash,
                  otp: '',
                  phone: props.phone,
                })
              }
            >
              {t(`${ENROLLMENT_STEP4_NAMESPACE}resendOTP`)}
            </span>
          </div>
        </div>
      </form>
    )}
  </Formik>
);

const mapStateToProps = state => ({
  step3Hash: state.Enrollment.userToEnroll.step3Hash,
  activityId: state.Enrollment.userToEnroll.activityId,
  phone: state.Enrollment.userToEnroll.phone,
  otp: state.Enrollment.userToEnroll.otp,
});

const mapDispatchToProps = dispatch => ({
  step4: step4Data => {
    dispatch(step4(step4Data));
  },
  resendOTP: resendOTPData => {
    dispatch(retryStep3(resendOTPData));
  },
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(EnrollmentStep4),
);
