import { actionCreator } from '../../utils/generators';
import produce from 'immer';
import types from './favorites.types';

const defaultState = {
  isLoading: false,
  error: null,
  favorites: [],
};
const favoritesReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', types.GET_FAVORITES):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', types.GET_FAVORITES):
      draft.favorites = action.payload;
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', types.GET_FAVORITES):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    case actionCreator('req', types.DELETE_FAVORITE):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', types.DELETE_FAVORITE):
      const { transactionId, transactionType } = action.payload;
      let currentFavorites = Object.values(
        JSON.parse(JSON.stringify(draft.favorites)),
      );
      draft.favorites = currentFavorites
        .filter(
          transaction =>
            transaction.transactionType === transactionType &&
            transaction.transactionId !== transactionId,
        )
        .slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', types.DELETE_FAVORITE):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    default:
      break;
  }
}, defaultState);

export default favoritesReducer;
