import produce from 'immer';
import { actionCreator } from '../../utils/generators';
import LocalisationsActionTypes from './localisations.types';

const defaultState = {
  error: null,
  gabsInfo: [],
  branchesInfo: [],
  activeTab: 'branch',
  branchFilteredLocations: [],
  gabFilteredLocations: [],
  selectedMark: null,
  hoveredMark: null,
  fitBoundFlag: true,
  mapZoom: 10,
};

const locationsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', LocalisationsActionTypes.SET_SELECTED_MARK):
      draft.selectedMark = action.payload;
      break;
    case actionCreator('req', LocalisationsActionTypes.SET_MAP_ZOOM):
      draft.mapZoom = action.payload;
      break;
    case actionCreator('req', LocalisationsActionTypes.SET_BOUND_FLAG):
      draft.fitBoundFlag = action.payload;
      break;
    case actionCreator('req', LocalisationsActionTypes.SET_HOVERED_MARK):
      draft.hoveredMark = action.payload;
      break;
    case actionCreator('req', LocalisationsActionTypes.SET_ACTIVE_TAB):
      draft.activeTab = action.payload;
      break;
    case actionCreator(
      'req',
      LocalisationsActionTypes.SET_BRANCH_FILTERED_LOCATIONS,
    ):
      draft.branchFilteredLocations = action.payload;
      break;
    case actionCreator(
      'req',
      LocalisationsActionTypes.SET_GAB_FILTERED_LOCATIONS,
    ):
      draft.gabFilteredLocations = action.payload;
      break;
    case actionCreator('req', LocalisationsActionTypes.GET_GAB_LOCALISATIONS):
    case actionCreator(
      'req',
      LocalisationsActionTypes.GET_BRANCHES_LOCALISATIONS,
    ):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('res', LocalisationsActionTypes.GET_GAB_LOCALISATIONS):
      draft.error = null;
      draft.loading = false;
      draft.gabsInfo = action.payload.data;
      break;
    case actionCreator('fail', LocalisationsActionTypes.GET_GAB_LOCALISATIONS):
    case actionCreator(
      'fail',
      LocalisationsActionTypes.GET_BRANCHES_LOCALISATIONS,
    ):
      draft.error = 'error found';
      draft.loading = false;
      break;
    case actionCreator(
      'res',
      LocalisationsActionTypes.GET_BRANCHES_LOCALISATIONS,
    ):
      draft.error = null;
      draft.loading = false;
      draft.branchesInfo = action.payload.data;
      break;
    default:
      break;
  }
}, defaultState);
export default locationsReducer;
