import React, { createContext } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import Header from '../Navigation/Header/Header';
import SideMenu from '../Navigation/SideMenu/SideMenu';
import { t } from '../../i18n';
import { CONNECTED_FOOTER_NAMESPACE, COMPONENTS_NAMESPACE } from './constants';
import ScrollButton from '../ScrollUpButton/ScrollUpButton';
import {
  hideMessage,
  showMessage,
} from '../../redux/messages/messages.actions';
import { logout } from '../../redux/login/login.actions';

const ConnectedLayoutContext = createContext({
  showMenu: true,
  toggleMenu: () => {},
});
const MainContent = styled.div.attrs(props => ({
  className: `bcp-content ${props.showMenu ? '' : 'no-margin'}`,
}))``;

class ConnectedLayout extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      showMenu: true,
      toggleMenu: this._toggleMenu,
      fullDiv: 'page-header',
    };
    this._logoutTimeout = null;
    this._warnTimeout = null;
  }
  _toggleMenu = fullDiv => {
    this.setState({
      showMenu: !this.state.showMenu,
      fullDiv: `page-header ${fullDiv}`,
    });
  };
  _setWarnTimeout = () => {
    this._warnTimeout = setTimeout(this._warn, 60 * 1000);
  };
  _clearWarnTimeout = () => {
    if (this._warnTimeout) clearTimeout(this._warnTimeout);
  };
  _resetWarnTimeout = () => {
    this._clearWarnTimeout();
    this._setWarnTimeout();
  };
  _setLogoutTimeout = () => {
    this._logoutTimeout = setTimeout(this._logout, 10 * 1000);
  };
  _clearLogoutTimeout = () => {
    if (this._logoutTimeout) clearTimeout(this._logoutTimeout);
  };
  _resetLogoutTimeout = () => {
    this._clearLogoutTimeout();
    this._setLogoutTimeout();
  };
  _createWarnTimeout = () => {
    this.events = [
      'load',
      'mousemove',
      'mousedown',
      'click',
      'scroll',
      'keypress',
    ];
    for (let event of this.events) {
      window.addEventListener(event, this._resetWarnTimeout);
    }
  };
  _destroyWarnTimeout = () => {
    this._clearWarnTimeout();
    for (let event of this.events) {
      window.removeEventListener(event, this._resetWarnTimeout);
    }
  };
  _logout = () => {
    this.props.logout();
  };
  _warn = () => {
    if (this.props.messageShown) this.props.hideMessage();
    this.props.showMessage(
      t(`${COMPONENTS_NAMESPACE}inactivityWarning`),
      'WARN',
      t(`${COMPONENTS_NAMESPACE}continue`),
      false,
      this._continueNavigation,
    );
    this._destroyWarnTimeout();
    this._resetLogoutTimeout();
  };
  _continueNavigation = () => {
    this.props.hideMessage();
  };
  componentDidMount() {
    this._createWarnTimeout();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      prevProps.messageShown !== this.props.messageShown &&
      !this.props.messageShown
    ) {
      this._clearLogoutTimeout();
      this._createWarnTimeout();
    }
  }
  componentWillUnmount() {
    this._destroyWarnTimeout();
  }
  render() {
    return (
      <ConnectedLayoutContext.Provider value={this.state}>
        <div className={this.state.fullDiv}>
          <Header />
          <SideMenu />
        </div>
        <MainContent showMenu={this.state.showMenu}>
          {this.props.children}
          <ScrollButton showAt={20} speed={2000} easing="easeInOutSine">
            <span className="scroll-to-top" />
          </ScrollButton>
        </MainContent>
        <div className="page-footer">
          <div className="footer-interne">
            <ul>
              <li> {t(`${CONNECTED_FOOTER_NAMESPACE}copyrightTitle`)} </li>
              <li>
                <Link to="#">
                  {t(`${CONNECTED_FOOTER_NAMESPACE}legalNotice`)}
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </ConnectedLayoutContext.Provider>
    );
  }
}
export const ConnectedLayoutConsumer = ConnectedLayoutContext.Consumer;

const mapStateToProps = state => ({
  messageShown: state.Messages.shown,
});
const mapDispatchToProps = dispatch => ({
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
  logout: () => {
    dispatch(logout());
  },
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(ConnectedLayout),
);
