import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { t } from '../../../../../i18n';
import { withTranslation } from 'react-i18next';
import UpdatePasswordModal from './UpdatePasswordModal';
import { toggleUpdatePasswordModal } from '../../../../../redux/menu/menu.actions';
import { showMessage } from '../../../../../redux/messages/messages.actions';

class ProfileMenu extends React.PureComponent {
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      prevProps.passwordIsChanged !== this.props.passwordIsChanged &&
      this.props.passwordIsChanged
    ) {
      this.props.showMessage(
        t('Components:passwordChanged'),
        'CONFIRM',
        t('Components:validate'),
        false,
      );
    }
  }
  render() {
    return (
      <div>
        <ul className="dropdown-menu">
          <li>
            <Link onClick={this.props.toggleUpdatePasswordModal}>
              <span className="icon mdi mdi-settings" />
              {t('Components:modifyPassword')}
            </Link>
          </li>
          <li>
            <Link to="/login">
              <span className="icon mdi mdi-power" />
              {t('Components:logout')}
            </Link>
          </li>
        </ul>
        {this.props.showUpdatePasswordModal ? (
          <UpdatePasswordModal
            show={this.props.showUpdatePasswordModal}
            handleClose={this.props.toggleUpdatePasswordModal}
          />
        ) : null}
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  toggleUpdatePasswordModal: () => {
    dispatch(toggleUpdatePasswordModal());
  },
  showMessage: (text, severity, validateTitle, hasCancelBtn) => {
    dispatch(showMessage(text, severity, validateTitle, hasCancelBtn));
  },
});
const mapStateToProps = state => ({
  showUpdatePasswordModal: state.Menu.showUpdatePasswordModal,
  passwordIsChanged: state.Menu.passwordIsChanged,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(ProfileMenu),
);
