import AxiosAPI from '../../rest/AxiosAPI';
import Config from '../../rest/Config';
import { ErrorHandler } from './ErrorHandler';

export default class Enrollment {
  _handleOnSuccess(response, resolver) {
    return resolver({ data: response.data, status: response.status });
  }
  _handleOnError(error, reject) {
    return reject(ErrorHandler(error));
  }

  step1 = step1Data =>
    new Promise((resolver, reject) =>
      new AxiosAPI(false)
        .setUrl(Config.Enrollment.STEP1)
        .setData(step1Data)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  step2 = step2Data =>
    new Promise((resolver, reject) =>
      new AxiosAPI(false)
        .setUrl(Config.Enrollment.STEP2)
        .setData(step2Data)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  step3 = step3Data =>
    new Promise((resolver, reject) =>
      new AxiosAPI(false)
        .setUrl(Config.Enrollment.STEP3)
        .setData(step3Data)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  retryStep3 = retryStep3Data =>
    new Promise((resolver, reject) =>
      new AxiosAPI(false)
        .setUrl(Config.Enrollment.RETRY_STEP3)
        .setData(retryStep3Data)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
  step4 = step4Data =>
    new Promise((resolver, reject) =>
      new AxiosAPI(false)
        .setUrl(Config.Enrollment.STEP4)
        .setData(step4Data)
        .sendPost()
        .then(response => this._handleOnSuccess(response, resolver))
        .catch(error => this._handleOnError(error, reject)),
    );
}
