import { put, takeLatest, call, all } from 'redux-saga/effects';
import Public from '../../services/apis/public';
import { actionCreator } from '../../utils/generators';
import { sagaWithLoader } from '../template/sagaWithLoader';
import types from './checks.types';
import Checks from '../../services/apis/checks';

function* getBranches() {
  try {
    const branches = yield call(new Public().getBranches);
    yield put({
      type: actionCreator('res', types.GET_BRANCHES),
      payload: branches.data,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_BRANCHES),
      }),
    ]);
  }
}

function* getOrders() {
  try {
    const orders = yield call(new Checks().mockGetOrders);
    yield put({
      type: actionCreator('res', types.GET_ORDERS),
      payload: orders.data,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_ORDERS),
      }),
    ]);
  }
}

function* step1(step1Action) {
  try {
    yield all([
      put({ type: actionCreator('req', types.NEXT_STEP) }),
      put({
        type: actionCreator('res', types.STEP1),
        payload: step1Action.payload,
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP1),
      payload: error.payload.text,
    });
  }
}

function* step2(step2Action) {
  try {
    yield all([
      put({ type: actionCreator('req', types.NEXT_STEP) }),
      put({
        type: actionCreator('res', types.STEP2),
        payload: step2Action.payload,
      }),
    ]);
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP2),
      payload: error.payload.text,
    });
  }
}
function* step3(step3Action) {
  try {
    const step3Response = yield call(
      new Checks().mockSaveOrder,
      step3Action.payload,
    );
    yield put({
      type: actionCreator('res', types.STEP3),
      payload: step3Response.data,
    });
  } catch (error) {
    yield put({
      type: actionCreator('fail', types.STEP3),
      payload: error.payload.text,
    });
  }
}

export function* checksSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_BRANCHES),
    sagaWithLoader,
    getBranches,
  );
  yield takeLatest(
    actionCreator('req', types.GET_ORDERS),
    sagaWithLoader,
    getOrders,
  );
  yield takeLatest(actionCreator('req', types.STEP1), sagaWithLoader, step1);
  yield takeLatest(actionCreator('req', types.STEP2), sagaWithLoader, step2);
  yield takeLatest(actionCreator('req', types.STEP3), sagaWithLoader, step3);
}
