import { put, takeLatest, call, all, select } from 'redux-saga/effects';
import Cards from '../../services/apis/Cards';
import { actionCreator } from '../../utils/generators';
import types from './cards.types';
import {
  getSelectedCardNumber,
  getSelectedCard,
  getSelectedCardProductCode,
} from './cards.selectors';
import { sagaWithLoader } from '../template/sagaWithLoader';

function* getCards() {
  try {
    const cards = yield call(new Cards().getCards);
    yield put({
      type: actionCreator('res', types.GET_CARDS),
      payload: cards.data,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_CARDS),
      }),
    ]);
  }
}
function* getCardTransactions() {
  try {
    const cardTransactions = yield call(
      new Cards().getCardTransactions,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.GET_TRANSACTIONS),
      payload: cardTransactions,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_TRANSACTIONS),
      }),
    ]);
  }
}
function* getCardLimits() {
  try {
    const response = yield call(
      new Cards().getCardLimits,
      yield select(getSelectedCardNumber),
      yield select(getSelectedCardProductCode),
    );
    yield put({
      type: actionCreator('res', types.GET_LIMITS),
      payload: response.data,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.GET_LIMITS),
      }),
    ]);
  }
}
function* editLimit(editLimitAction) {
  try {
    yield call(
      new Cards().editLimit,
      editLimitAction.payload,
      yield select(getSelectedCardNumber),
      yield select(getSelectedCardProductCode),
    );
    yield put({
      type: actionCreator('res', types.EDIT_LIMIT),
      payload: editLimitAction.payload,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.EDIT_LIMIT),
      }),
    ]);
  }
}

function* switchCardState() {
  let selectedCard = yield select(getSelectedCard);
  try {
    if (selectedCard) {
      let serviceToCall = selectedCard.isActive
        ? new Cards().deactivateCard
        : new Cards().activateCard;
      const response = yield call(serviceToCall, selectedCard.cardNumber);
      yield put({
        type: actionCreator('res', types.SWITCH_STATE),
        payload: response,
      });
    }
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.SWITCH_STATE),
      }),
    ]);
  }
}
function* oppose() {
  try {
    const response = yield call(
      new Cards().opposeCard,
      yield select(getSelectedCardNumber),
    );
    yield put({
      type: actionCreator('res', types.OPPOSE),
      payload: response,
    });
  } catch (error) {
    yield all([
      put(error),
      put({
        type: actionCreator('fail', types.OPPOSE),
      }),
    ]);
  }
}

export function* cardsSagas() {
  yield takeLatest(
    actionCreator('req', types.GET_CARDS),
    sagaWithLoader,
    getCards,
  );
  yield takeLatest(
    actionCreator('req', types.GET_TRANSACTIONS),
    sagaWithLoader,
    getCardTransactions,
  );
  yield takeLatest(
    actionCreator('req', types.GET_LIMITS),
    sagaWithLoader,
    getCardLimits,
  );
  yield takeLatest(
    actionCreator('req', types.EDIT_LIMIT),
    sagaWithLoader,
    editLimit,
  );
  yield takeLatest(
    actionCreator('req', types.SWITCH_STATE),
    sagaWithLoader,
    switchCardState,
  );
  yield takeLatest(actionCreator('req', types.OPPOSE), sagaWithLoader, oppose);
}
