import React from 'react';
import { Link } from 'react-router-dom';
import { t } from 'i18n';
import { HEADER_BRAND } from '../constants';
import { ConnectedLayoutConsumer } from '../../../ConnectedLayout/ConnectedLayout';
import HeaderLogoSvg from '../../../../assets/SvgComponents/HeaderSvg/HeaderLogoSvg';

const headerBrand = () => (
  <ConnectedLayoutConsumer>
    {({ toggleMenu }) => (
      <div className="navbar-header">
        <Link className="navbar-brand" to="/dashboard">
          <HeaderLogoSvg />
        </Link>
        <div className="c-header-icon js-hamburger">
          <div
            className="hamburger-toggle"
            onClick={() => {
              toggleMenu('full-div');
            }}
          >
            <span className="bar-top" />
            <span className="bar-mid" />
            <span className="bar-bot" />
          </div>
          <span
            onClick={() => {
              toggleMenu('full-div');
            }}
          >
            {t(`${HEADER_BRAND}menu`)}
          </span>
        </div>
      </div>
    )}
  </ConnectedLayoutConsumer>
);

export default headerBrand;
