import React from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import * as Yup from 'yup';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import classes from '../styles/Style.module.css';
import { ENROLLMENT_STEP3_NAMESPACE } from '../constants';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
import { step3 } from '../../../redux/enrollment/enrollment.actions';

const invalidInputClass = `form-control ${classes.Invalid}`;

const validationSchema = Yup.object().shape({
  password: Yup.string()
    .required(t(`${ENROLLMENT_STEP3_NAMESPACE}passwordRequiredMsg`))
    .min(8, t(`${ENROLLMENT_STEP3_NAMESPACE}passwordMinLengthMsg`))
    .max(12, t(`${ENROLLMENT_STEP3_NAMESPACE}passwordMaxLengthMsg`))
    .matches(
      /^(?=.*[a-z]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordLowerCase`),
    )
    .matches(
      /^(?=.*[A-Z]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordUpperCase`),
    )
    .matches(
      /^(?=.*\d).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordNumber`),
    )
    .matches(
      /^(?=.*[@$!%*?&]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordSpecialCharacter`),
    ),
  confirmPassword: Yup.string()
    .required(t(`${ENROLLMENT_STEP3_NAMESPACE}confirmPasswordRequiredMsg`))
    .test(
      'confirm password validation',
      t(`${ENROLLMENT_STEP3_NAMESPACE}passwordConfirmationIncorrect`),
      function(value) {
        const { password } = this.parent;
        return password === value;
      },
    ),
});

class EnrollmentStep3 extends React.PureComponent {
  state = { showPasswords: false };
  _showPassword = () => {
    this.setState({ showPasswords: true });
  };
  _hidePassword = () => {
    this.setState({ showPasswords: false });
  };
  componentDidMount() {
    document.addEventListener('mouseup', this._hidePassword);
  }
  componentWillUnmount() {
    document.removeEventListener('mouseup', this._hidePassword);
  }
  render() {
    return (
      <Formik
        ref={ref => this.props.formRefSetter(ref)}
        validationSchema={validationSchema}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          this.props.step3({
            deviceId: 'web',
            hash: this.props.step2Hash,
            password: values.password,
            phone: this.props.phone,
          });
        }}
      >
        {({ handleSubmit, handleChange, handleBlur, values, errors }) => (
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label className="col-sm-12 col-md-4 control-label">
                {t(`${ENROLLMENT_STEP3_NAMESPACE}password`)}
                <sup>*</sup>
              </label>
              <div className="col-md-8 col-sm-12">
                <input
                  className={
                    errors.password ? invalidInputClass : 'form-control'
                  }
                  name="password"
                  type={this.state.showPasswords ? 'text' : 'password'}
                  placeholder={t(
                    `${ENROLLMENT_STEP3_NAMESPACE}passwordPlaceholder`,
                  )}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.password}
                />
                <span className="input-show-btn">
                  <button
                    className="btn reveal"
                    type="button"
                    onMouseDown={this._showPassword}
                    onTouchStart={this._showPassword}
                    onTouchEnd={this._hidePassword}
                  />
                </span>
                <ExpiringFormFieldError name="password" />
              </div>
            </div>
            <div className="form-group">
              <label className="col-sm-12 col-md-4 control-label">
                {t(`${ENROLLMENT_STEP3_NAMESPACE}confirmPassword`)}
              </label>
              <div className="col-md-8 col-sm-12">
                <input
                  className={
                    errors.confirmPassword ? invalidInputClass : 'form-control'
                  }
                  name="confirmPassword"
                  type={this.state.showPasswords ? 'text' : 'password'}
                  placeholder={t(
                    `${ENROLLMENT_STEP3_NAMESPACE}confirmPasswordPlaceholder`,
                  )}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.confirmPassword}
                />
                <ExpiringFormFieldError name="confirmPassword" />
              </div>
            </div>
            <p className="champs_obligatoires">
              <sup>(*)</sup>
              {t(`${ENROLLMENT_STEP3_NAMESPACE}passwordForm`)}
            </p>
          </form>
        )}
      </Formik>
    );
  }
}
const mapStateToProps = state => ({
  step2Hash: state.Enrollment.userToEnroll.step2Hash,
  phone: state.Enrollment.userToEnroll.phone,
});

const mapDispatchToProps = dispatch => ({
  step3: step3Data => {
    dispatch(step3(step3Data));
  },
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(EnrollmentStep3),
);
