import { actionCreator } from '../../utils/generators';
import types from './enrollment.types';

export const step1 = step1Data => ({
  type: actionCreator('req', types.STEP1),
  payload: step1Data,
});
export const step2 = step2Data => ({
  type: actionCreator('req', types.STEP2),
  payload: step2Data,
});
export const step3 = step3Data => ({
  type: actionCreator('req', types.STEP3),
  payload: step3Data,
});
export const retryStep3 = retryStep3Data => ({
  type: actionCreator('req', types.RETRY_STEP3),
  payload: retryStep3Data,
});
export const step4 = step4Data => ({
  type: actionCreator('req', types.STEP4),
  payload: step4Data,
});
export const toggleEnrollmentModal = () => ({
  type: actionCreator('req', types.TOGGLE_MODAL),
});
export const previousStep = () => ({
  type: actionCreator('req', types.PREVIOUS_STEP),
});
export const fillUserData = data => ({
  type: actionCreator('req', types.FILL_USER_DATA),
  payload: data,
});
