import { actionCreator } from '../../utils/generators';
import OperationsActionTypes from './operations.types';
import produce from 'immer';

const defaultState = {
  isLoading: false,
  error: null,
  operations: [],
  allAccountsOperations: [],
  pickedAccount: null,
};
const operationsReducer = produce((draft, action) => {
  switch (action.type) {
    case actionCreator('req', OperationsActionTypes.GET_OPERATIONS):
    case actionCreator(
      'req',
      OperationsActionTypes.GET_ALL_ACCOUNTS_OPERATIONS,
    ):
      draft.error = null;
      draft.loading = true;
      break;
    case actionCreator('req', OperationsActionTypes.SET_PICKED_ACCOUNT):
      draft.pickedAccount = action.payload;
      break;
    case actionCreator('res', OperationsActionTypes.GET_OPERATIONS):
      let operations = action.payload.data;
      draft.operations = operations.slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator(
      'res',
      OperationsActionTypes.GET_ALL_ACCOUNTS_OPERATIONS,
    ):
      let allAccountsOperations = action.payload.data;
      draft.allAccountsOperations = allAccountsOperations.slice();
      draft.error = null;
      draft.isLoading = false;
      break;
    case actionCreator('fail', OperationsActionTypes.GET_OPERATIONS):
      draft.operations = [];
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    case actionCreator(
      'fail',
      OperationsActionTypes.GET_ALL_ACCOUNTS_OPERATIONS,
    ):
      draft.error = 'error found';
      draft.isLoading = false;
      break;
    default:
      break;
  }
}, defaultState);

export default operationsReducer;
