import React from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import * as Yup from 'yup';
import { Calendar } from 'primereact/calendar';
import {
  fillUserData,
  step2,
} from '../../../redux/enrollment/enrollment.actions';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { ENROLLMENT_STEP2_NAMESPACE } from '../constants';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
import classes from '../../Transfers/styles/Style.module.css';
import { formatDateTime } from '../../../utils/DateUtils';
import {
  getPhoneIndex,
  getPhoneWithoutIndex,
} from '../../../redux/enrollment/enrollment.selectors';
import { Link } from 'react-router-dom';

const renderValidationSchema = bankId => {
  const accountLength = bankId === 1 || bankId === 5 ? 12 : 11;
  const accountLengthRegExp = new RegExp(`^\\d{${accountLength}}$`);
  return Yup.object().shape({
    birthdate: Yup.string().required(
      t(`${ENROLLMENT_STEP2_NAMESPACE}requiredBirthdate`),
    ),
    email: Yup.string()
      .email(t(`${ENROLLMENT_STEP2_NAMESPACE}emailNotValid`))
      .required(t(`${ENROLLMENT_STEP2_NAMESPACE}emailRequiredMsg`)),
    accountNumber: Yup.string()
      .required(t(`${ENROLLMENT_STEP2_NAMESPACE}requiredAccountNumber`))
      .matches(/^\d+$/, t(`${ENROLLMENT_STEP2_NAMESPACE}numericAccountNumber`))
      .matches(
        accountLengthRegExp,
        t(`${ENROLLMENT_STEP2_NAMESPACE}invalidAccountNumberLength`, {
          accountLength,
        }),
      ),
    acceptConditions: Yup.boolean()
      .required(t(`${ENROLLMENT_STEP2_NAMESPACE}acceptConditionsRequired`))
      .oneOf(
        [true],
        t(`${ENROLLMENT_STEP2_NAMESPACE}acceptConditionsRequired`),
      ),
  });
};
const invalidInputClass = `form-control ${classes.Invalid}`;
const CALENDAR_NAMESPACE = 'Components:calendar.';
class EnrollmentStep2 extends React.PureComponent {
  state = { birthdate: null };
  _setBirthdate = date => {
    this.setState({ birthdate: date });
  };
  render() {
    const dayNames = [
      t(`${CALENDAR_NAMESPACE}sunday`),
      t(`${CALENDAR_NAMESPACE}monday`),
      t(`${CALENDAR_NAMESPACE}tuesday`),
      t(`${CALENDAR_NAMESPACE}wednesday`),
      t(`${CALENDAR_NAMESPACE}thursday`),
      t(`${CALENDAR_NAMESPACE}friday`),
      t(`${CALENDAR_NAMESPACE}saturday`),
    ];
    const dayNamesShort = [
      t(`${CALENDAR_NAMESPACE}sun`),
      t(`${CALENDAR_NAMESPACE}mon`),
      t(`${CALENDAR_NAMESPACE}tu`),
      t(`${CALENDAR_NAMESPACE}wed`),
      t(`${CALENDAR_NAMESPACE}thu`),
      t(`${CALENDAR_NAMESPACE}fri`),
      t(`${CALENDAR_NAMESPACE}sat`),
    ];
    const dayNamesMin = [
      t(`${CALENDAR_NAMESPACE}s`),
      t(`${CALENDAR_NAMESPACE}m`),
      t(`${CALENDAR_NAMESPACE}t`),
      t(`${CALENDAR_NAMESPACE}w`),
      t(`${CALENDAR_NAMESPACE}thues`),
      t(`${CALENDAR_NAMESPACE}f`),
      t(`${CALENDAR_NAMESPACE}sund`),
    ];
    const monthNames = [
      t(`${CALENDAR_NAMESPACE}january`),
      t(`${CALENDAR_NAMESPACE}february`),
      t(`${CALENDAR_NAMESPACE}march`),
      t(`${CALENDAR_NAMESPACE}april`),
      t(`${CALENDAR_NAMESPACE}may`),
      t(`${CALENDAR_NAMESPACE}june`),
      t(`${CALENDAR_NAMESPACE}july`),
      t(`${CALENDAR_NAMESPACE}august`),
      t(`${CALENDAR_NAMESPACE}september`),
      t(`${CALENDAR_NAMESPACE}october`),
      t(`${CALENDAR_NAMESPACE}november`),
      t(`${CALENDAR_NAMESPACE}december`),
    ];
    const monthNamesShort = [
      t(`${CALENDAR_NAMESPACE}jan`),
      t(`${CALENDAR_NAMESPACE}feb`),
      t(`${CALENDAR_NAMESPACE}ma`),
      t(`${CALENDAR_NAMESPACE}apr`),
      t(`${CALENDAR_NAMESPACE}mays`),
      t(`${CALENDAR_NAMESPACE}ju`),
      t(`${CALENDAR_NAMESPACE}jul`),
      t(`${CALENDAR_NAMESPACE}aug`),
      t(`${CALENDAR_NAMESPACE}sep`),
      t(`${CALENDAR_NAMESPACE}oct`),
      t(`${CALENDAR_NAMESPACE}nov`),
      t(`${CALENDAR_NAMESPACE}dec`),
    ];
    const calendarLocale = {
      dayNames: dayNames,
      dayNamesShort: dayNamesShort,
      dayNamesMin: dayNamesMin,
      monthNames: monthNames,
      monthNamesShort: monthNamesShort,
      today: t(`${CALENDAR_NAMESPACE}today`),
      clear: t(`${CALENDAR_NAMESPACE}clear`),
    };
    const initialValues = {
      birthdate:
        this.props.birthdate.length > 0 ? new Date(this.props.birthdate) : '',
      email: this.props.email,
      accountNumber: this.props.accountNumber,
    };
    return (
      <Formik
        ref={ref => this.props.formRefSetter(ref)}
        initialValues={initialValues}
        validationSchema={renderValidationSchema(this.props.bank)}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          const birthdate =
            values.birthdate instanceof Date
              ? this.props.birthdate
              : values.birthdate;
          this.props.fillUserData({
            birthdate: birthdate,
            email: values.email,
            accountNumber: values.accountNumber,
          });
          this.props.step2({
            accountNumber: values.accountNumber,
            bank: this.props.bank,
            birthdate: birthdate,
            email: values.email,
            phone: this.props.phone,
          });
        }}
      >
        {({
          handleSubmit,
          values,
          handleChange,
          handleBlur,
          errors,
          setFieldValue,
        }) => (
          <form onSubmit={handleSubmit}>
            <p>{t(`${ENROLLMENT_STEP2_NAMESPACE}fillAccountInformation`)} </p>
            <div className="form-group">
              <label className="col-sm-12 col-md-4 control-label">
                {t(`${ENROLLMENT_STEP2_NAMESPACE}birthdate`)}
              </label>
              <div className="col-md-8 col-sm-12">
                <Calendar
                  name="birthdate"
                  locale={calendarLocale}
                  readOnlyInput={true}
                  dateFormat="yy-mm-dd"
                  placeholder={t(`${ENROLLMENT_STEP2_NAMESPACE}birthdate`)}
                  value={this.state.birthdate || initialValues.birthdate}
                  onChange={e => {
                    const chosenDate = formatDateTime(e.value, 'Y-MM-DD');
                    setFieldValue('birthdate', chosenDate);
                    this._setBirthdate(chosenDate);
                  }}
                  showIcon={true}
                  monthNavigator={true}
                  yearNavigator={true}
                  yearRange="1900:2010"
                />
                <ExpiringFormFieldError name="birthdate" />
              </div>
            </div>
            <div className="form-group col-md-12 col-sm-12">
              <label className="col-sm-12 col-md-4 control-label">
                {t(`${ENROLLMENT_STEP2_NAMESPACE}email`)}
              </label>
              <div className="col-sm-12 col-md-8">
                <input
                  className={errors.email ? invalidInputClass : 'form-control'}
                  name="email"
                  type="email"
                  value={values.email}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(`${ENROLLMENT_STEP2_NAMESPACE}email`)}
                />
                <ExpiringFormFieldError name="email" />
              </div>
            </div>
            <div className="form-group col-md-12 col-sm-12">
              <label className="col-sm-12 col-md-4 control-label">
                {t(`${ENROLLMENT_STEP2_NAMESPACE}accountNumber`)}
              </label>
              <div className="col-sm-12 col-md-8">
                <input
                  className={
                    errors.accountNumber ? invalidInputClass : 'form-control'
                  }
                  name="accountNumber"
                  type="text"
                  value={values.accountNumber}
                  onChange={handleChange}
                  onBlur={handleBlur}
                  placeholder={t(`${ENROLLMENT_STEP2_NAMESPACE}accountNumber`)}
                />
                <ExpiringFormFieldError name="accountNumber" />
              </div>
            </div>
            <div className="bcp-checkbox inline">
              <input
                id="acceptConditions"
                name="acceptConditions"
                type="checkbox"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.acceptConditions}
              />

              <label for="acceptConditions">
                <Link to="/CGU" target="_blank" style={{ color: '#666666' }}>
                  {t(`${ENROLLMENT_STEP2_NAMESPACE}acceptConditions`)}
                </Link>
              </label>
              {errors.acceptConditions ? (
                <div>
                  **
                  {t(`${ENROLLMENT_STEP2_NAMESPACE}acceptConditionsRequired`)}
                </div>
              ) : null}
            </div>
          </form>
        )}
      </Formik>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  step2: step2Data => {
    dispatch(step2(step2Data));
  },
  fillUserData: data => {
    dispatch(fillUserData(data));
  },
});
const mapStateToProps = state => ({
  phoneIndex: getPhoneIndex(state),
  phoneWithoutIndex: getPhoneWithoutIndex(state),
  bank: state.Enrollment.userToEnroll.bank,
  phone: state.Enrollment.userToEnroll.phone,
  accountNumber: state.Enrollment.userToEnroll.accountNumber,
  email: state.Enrollment.userToEnroll.email,
  birthdate: state.Enrollment.userToEnroll.birthdate,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(EnrollmentStep2),
);
