import types from './menu.types';
import { actionCreator } from '../../utils/generators';

export const activateAccountsTab = activeTab => ({
  type: types.ACTIVATE_ACCOUNTS_TAB,
  payload: activeTab,
});
export const activateTransfersTab = activeTab => ({
  type: types.ACTIVATE_TRANSFERS_TAB,
  payload: activeTab,
});
export const activateSimulatorTab = activeTab => ({
  type: types.ACTIVATE_SIMULATOR_TAB,
  payload: activeTab,
});
export const activateCardsTab = activeTab => ({
  type: types.ACTIVATE_CARDS_TAB,
  payload: activeTab,
});

export const activateChecksTab = activeTab => ({
  type: types.ACTIVATE_CHECKS_TAB,
  payload: activeTab,
});

export const activateTabWithParams = activateTabRequest => ({
  type: types.ACTIVATE_TAB_WITH_PARAMS,
  payload: activateTabRequest,
});

export const updatePassword = updatingData => ({
  type: actionCreator('req', types.UPDATE_PASSWORD_ACTION),
  payload: updatingData,
});

export const toggleUpdatePasswordModal = () => ({
  type: actionCreator('req', types.TOGGLE_UPDATE_PASSWORD_MODAL),
});
