import React from 'react';
import FormModal from '../../../components/Ui/CustomModal/FormModal';
import Stepper from './Stepper';
import CurrentEnrollmentStep from './CurrentEnrollmentStep';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { ENROLLMENT_NAMESPACE } from '../constants';
import Alert from '../../../components/Ui/Alert/Alert';

class Feature extends React.PureComponent {
  state = { showAlert: false };
  formRef = null;
  _formRefSetter = ref => {
    this.formRef = ref;
  };
  _validateCurrentStep = () => {
    this.formRef.submitForm();
  };
  _backStep = () => {
    if (this.props.activeStep > 1) {
      this.props.previousStep();
    }
  };
  _handleClose = () => {
    this.setState({ showAlert: false }, this.props.toggleModal);
  };
  _hideAlert = () => this.setState({ showAlert: false });
  _renderAlert = () => {
    if (this.state.showAlert) {
      return (
        <Alert
          close={this._hideAlert}
          type="Default Danger"
          text={this.props.error}
          className="animated"
        />
      );
    }
  };
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.error !== this.props.error && this.props.error) {
      this.setState({ showAlert: true });
    }
    if (prevProps.activeStep !== this.props.activeStep) {
      this.setState({ showAlert: false });
    }
  }
  render() {
    return (
      <FormModal
        title={t(`${ENROLLMENT_NAMESPACE}title`)}
        show={this.props.modalShown}
        smallForm
        handlePrevious={this._backStep}
        handleClose={this._handleClose}
        handleValidate={this._validateCurrentStep}
        previousTitle={t(`${ENROLLMENT_NAMESPACE}previous`)}
        cancelTitle={t(`${ENROLLMENT_NAMESPACE}cancel`)}
        validateTitle={t(`${ENROLLMENT_NAMESPACE}validate`)}
        hasPreviousBtn={this.props.activeStep > 1}
        hasCancelBtn
        hasValidateBtn
        alert={this._renderAlert}
      >
        <div className="row wizard-row div__abonemment">
          <div className="col-md-12 fuelux">
            <div className="block-wizard panel panel-default">
              <div className="wizard wizard-ux no-steps-container">
                <Stepper activeStep={this.props.activeStep} />
                <div className="step-content">
                  <div className="step-pane active">
                    <CurrentEnrollmentStep
                      activeStep={this.props.activeStep}
                      formRefSetter={this._formRefSetter}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </FormModal>
    );
  }
}
export default withTranslation()(Feature);
