import React from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { connect } from 'react-redux';
import { Dropdown } from 'primereact/dropdown';
import {
  step1,
  fillUserData,
} from '../../../redux/enrollment/enrollment.actions';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import {
  ENROLLMENT_STEP1_NAMESPACE,
  banksOptions,
  phoneIndexOptions,
} from '../constants';
import {
  getPhoneIndex,
  getPhoneWithoutIndex,
} from '../../../redux/enrollment/enrollment.selectors';
import classes from '../styles/Style.module.css';

const dropdownTemplate = option => {
  let country = option.country || option.bank;
  if (country === 'nigerBIAN' || country === 'nigerBANE') country = 'niger';
  return (
    <>
      <img
        alt={country}
        src={require(`../../../assets/img/countries/${country}.svg`)}
        width="24"
      />
      <span>{option.label}</span>
    </>
  );
};
const renderValidationSchema = index => {
  const phoneLength = index
    ? phoneIndexOptions.filter(option => option.value === index)[0].phoneLength
    : 8;
  const phoneRegExp = new RegExp(`^\\d{${phoneLength}}$`);
  return Yup.object().shape({
    bank: Yup.string().required(t(`${ENROLLMENT_STEP1_NAMESPACE}bankRequired`)),
    phoneWithoutIndex: Yup.string()
      .required(t('Login:phoneNumberRequired'))
      .matches(phoneRegExp, t('Login:phoneError', { phoneLength })),
  });
};
const inputClasses = `form-control ${classes.Invalid}`;

class EnrollmentStep1 extends React.PureComponent {
  state = {
    chosenIndex: this.props.phoneIndex !== '' ? this.props.phoneIndex : '+225',
    chosenBank: this.props.bank,
  };
  render() {
    const initialValues = {
      phoneIndex: this.state.chosenIndex,
      phoneWithoutIndex: this.props.phoneWithoutIndex,
      bank: this.props.bank,
    };
    return (
      <Formik
        ref={ref => this.props.formRefSetter(ref)}
        initialValues={initialValues}
        validationSchema={renderValidationSchema(this.state.chosenIndex)}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          this.props.fillUserData({
            phone: values.phoneIndex + values.phoneWithoutIndex,
            bank: values.bank,
          });
          this.props.step1({
            phone: values.phoneIndex + values.phoneWithoutIndex,
          });
        }}
      >
        {({
          handleSubmit,
          setFieldValue,
          setTouched,
          values,
          handleChange,
          handleBlur,
          errors,
        }) => {
          const phoneClassName =
            errors.phoneIndex || errors.phoneWithoutIndex
              ? inputClasses
              : 'form-control';
          return (
            <form onSubmit={handleSubmit}>
              <div className="abone-form">
                <div className="form-pay-txt">
                  <div className="form-group select-pay">
                    <Dropdown
                      name="bank"
                      options={banksOptions}
                      value={this.state.chosenBank}
                      onChange={e => {
                        setFieldValue('bank', e.value);
                        this.setState({ chosenBank: e.value });
                      }}
                      onMouseDown={() => setTouched({ bank: true })}
                      placeholder={t(`${ENROLLMENT_STEP1_NAMESPACE}chooseBank`)}
                      itemTemplate={dropdownTemplate}
                    />
                    <ExpiringFormFieldError name="bank" />
                  </div>
                </div>
                <div className="form-tel-txt">
                  <div className="form-group select-telephone">
                    <Dropdown
                      name="phoneIndex"
                      options={phoneIndexOptions}
                      value={this.state.chosenIndex}
                      onChange={e => {
                        setFieldValue('phoneIndex', e.value);
                        this.setState({ chosenIndex: e.value });
                      }}
                      onMouseDown={() => setTouched({ phoneIndex: true })}
                      placeholder={'+225'}
                      itemTemplate={dropdownTemplate}
                    />
                  </div>
                  <div className="form-group">
                    <input
                      className={phoneClassName}
                      name="phoneWithoutIndex"
                      type="text"
                      placeholder={t('Login:phone')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.phoneWithoutIndex}
                    />
                    <ExpiringFormFieldError name="phoneIndex" />
                    <ExpiringFormFieldError name="phoneWithoutIndex" />
                  </div>
                </div>
              </div>
            </form>
          );
        }}
      </Formik>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  step1: step1Data => {
    dispatch(step1(step1Data));
  },
  fillUserData: data => {
    dispatch(fillUserData(data));
  },
});
const mapStateToProps = state => ({
  phoneIndex: getPhoneIndex(state),
  phoneWithoutIndex: getPhoneWithoutIndex(state),
  bank: state.Enrollment.userToEnroll.bank,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(EnrollmentStep1),
);
