import React from 'react';
import Header from './components/Header';
import Footer from './components/Footer';
import ScrollButton from '../ScrollUpButton/ScrollUpButton';
import Enrollment from '../../features/Enrollment';

class DisconnectedLayout extends React.PureComponent {
  render() {
    return (
      <>
        <Header />
        <div className="bcp-content-home">
          {this.props.children}
          <ScrollButton showAt={20} speed={2000} easing="easeInOutSine">
            <span className="scroll-to-top" />
          </ScrollButton>
          <Enrollment />
        </div>
        {this.props.hasFooter ? <Footer /> : null}
      </>
    );
  }
}
export default DisconnectedLayout;
