import { t } from '../../i18n';

export const ENROLLMENT_NAMESPACE = 'Enrollment:';
export const ENROLLMENT_STEP1_NAMESPACE = `${ENROLLMENT_NAMESPACE}step1.`;
export const ENROLLMENT_STEP2_NAMESPACE = `${ENROLLMENT_NAMESPACE}step2.`;
export const ENROLLMENT_STEP3_NAMESPACE = `${ENROLLMENT_NAMESPACE}step3.`;
export const ENROLLMENT_STEP4_NAMESPACE = `${ENROLLMENT_NAMESPACE}step4.`;
const banks = [
  'ivoryCoast',
  'benin',
  'togo',
  'burkinaFaso',
  'nigerBANE',
  'mali',
  'senegal',
  'nigerBIAN',
  'guineaBissau',
];

export const banksOptions = banks.map((bank, index) => {
  const bankId = index <= 6 ? index + 1 : index + 2;
  return {
    value: bankId,
    label: t(`${ENROLLMENT_STEP1_NAMESPACE}${bank}Label`),
    bank: bank,
  };
});

export const phoneIndexOptions = [
  { value: '+221', label: '+221', country: 'senegal', phoneLength: 9 },
  { value: '+223', label: '+223', country: 'mali', phoneLength: 8 },
  { value: '+224', label: '+224', country: 'guineaBissau', phoneLength: 8 },
  { value: '+225', label: '+225', country: 'ivoryCoast', phoneLength: 8 },
  { value: '+226', label: '+226', country: 'burkinaFaso', phoneLength: 8 },
  { value: '+227', label: '+227', country: 'niger', phoneLength: 8 },
  { value: '+228', label: '+228', country: 'togo', phoneLength: 8 },
  { value: '+229', label: '+229', country: 'benin', phoneLength: 8 },
];
